import './internal-utils';
import './query-cascade-step';
import $ from 'jquery';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryCascadeFilter = function (element, options) {
		this._init(element, options);
	};

	QueryCascadeFilter.prototype = {

		_defaultOptions: {
			includeAllSteps: false, // If true, the generated JSON will include all JSONs generated by all the steps
			noCascadeHelpText: false, // If true, there will not be help text in the start of the cascade
			property: Object()      // QueryProperty object
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);

			this.properties = this.options.property.properties;
			this.pipeline = this.options.property.cascade.map(function (p) { return module.queryProperty(p); });
			this.includeAllSteps = this.options.property.includeAllSteps;
			this.noCascadeHelpText = this.options.property.noCascadeHelpText;

			this.structure = {
				cascade: $('<div/>').addClass('as-query-cascade')
			};

			this.$element.addClass('as-query-cascade-filter').empty().append(this.structure.cascade);

			if (this.pipeline.length > 1 && !this.noCascadeHelpText) {
				this.$element.prepend('<div class="as-help-text as-help-text--space-left-2x as-help-text--space-right-1x">in</div>');
			}

			this.cascadeSteps = [];

			// Initialize the cascade
			this._initCascade();
		},

		_initCascade: function () {

			for (var i = 0; i < this.pipeline.length; i++) {
				(function (self, stepIndex) {

					var stepProperty = self.pipeline[stepIndex];

					var $cascadeStep = $('<div/>').addClass('as-query-cascade-step').attr('cascade-step', stepIndex);

					self.structure.cascade.append($cascadeStep);
					self.cascadeSteps.push($cascadeStep);

					// Initiate the cascade step
					var cascadeStepOptions = { property: stepProperty };
					if (stepProperty.cascadeOptionalStep) {
						cascadeStepOptions['hideOptionalStepCallback'] = function () {

							// In order to hide an optional step, we should call "cancelContinuation" on the
							// previous step
							module.queryCascadeStep(self.cascadeSteps[stepIndex - 1], 'cancelContinuation');
						};
					}
					module.queryCascadeStep($cascadeStep, cascadeStepOptions);

				})(this, i);
			}
		},

		_hideSteps: function (startingStepNum) {
			for (var stepNum = startingStepNum; stepNum < this.cascadeSteps.length; stepNum++) {
				module.queryCascadeStep(this.cascadeSteps[stepNum], 'hideStep');
			}
		},

		_showStep: function (stepNum, params, stepShownCallback) {
			var $cascadeStep = this.cascadeSteps[stepNum];

			// Register the step change event
			var changeCallback = this._generateChangeCallback(stepNum, params);
			module.queryCascadeStep($cascadeStep, 'registerChangeEvent', changeCallback);

			// Disable next steps if needed
			if (stepNum < this.cascadeSteps.length - 1)
				this._hideSteps(stepNum);

			// Show the cascade step
			var self = this;
			module.queryCascadeStep($cascadeStep, 'showStep', params, function () {
				// If the property of the current step is context bound, we should take the value from the context and
				// set it + call the step shown callback (if exists)
				var stepProperty = self.pipeline[stepNum];
				if (stepProperty.contextBindedPropName && (stepProperty.contextBindedPropName in module.globals.context)) {
					module.queryCascadeStep($cascadeStep, 'setVal',
						module.globals.context[stepProperty.contextBindedPropName], true, stepShownCallback);
				} else if (stepShownCallback) {
					stepShownCallback();
				}
			});
		},

		_generateChangeCallback: function (stepNum, params) {
			if (stepNum < this.cascadeSteps.length - 1) {
				const self = this;
				return function (name, selectedValue, nextStepShownCallback) {
					const nextStepNum = stepNum + 1;
					const nextStep = self.pipeline[nextStepNum];

					if (nextStep.shouldShowStep(selectedValue)) {
						const newParams = params ? Object.assign({}, params) : {};
						newParams[name] = selectedValue;
						self._showStep(nextStepNum, newParams, nextStepShownCallback);
					} else {
						self._hideSteps(nextStepNum);

						if (nextStepShownCallback) {
							nextStepShownCallback();
						}
					}
				};
			} else {
				return function (name, selectedValue, nextStepShownCallback) {
					// There is no next step so we call manually the nextStepShownCallback
					if (nextStepShownCallback) {
						nextStepShownCallback();
					}
				};
			}
		},

		_setStepState: function (stepNum, state, setStateDoneCallback) {
			module.queryCascadeStep(this.cascadeSteps[stepNum], 'setState', state, setStateDoneCallback);
		},

		_getStepQuery: function ($cascadeStep) {
			var stepQuery = {};
			var stepInnerQuery = module.queryCascadeStep($cascadeStep, 'generateJson');

			// Handle only non-empty step queries
			if (Object.keys(stepInnerQuery).length > 0) {

				// Add the property name (if needed)
				if (module.queryCascadeStep($cascadeStep, 'jsonContainsPropertyName')) {
					stepQuery = stepInnerQuery;
				} else {
					stepQuery[module.queryCascadeStep($cascadeStep, 'getName')] = stepInnerQuery;
				}
			}

			return stepQuery;
		},

		/******************
         * Public methods *
         ******************/

		getClassName: function () {
			return 'QueryCascadeFilter';
		},

		render: function (cascadeFilterRendered) {

			// Show the first step
			this._showStep(0, {}, cascadeFilterRendered);
		},

		setState: function (state, setStateDoneCallback) {
			// Recursive set step state method, in order to initialize all the cascade steps using callbacks
			// (each state is notifying when the state if fully set [including rendering the next step])
			var self = this;
			var setStepState = function (stepIndex) {
				// Check if we should set state for the current step index
				if (stepIndex < state.length) {
					var setStateFunc = function () {
						self._setStepState(stepIndex, state[stepIndex], function () {
							setStepState(stepIndex + 1);
						});
					};

					// If this cascade step is optional (for example, event properties), we have to "press" manually
					// on the continuation button of the previous step before setting the current step state
					if (self.pipeline[stepIndex].cascadeOptionalStep) {
						module.queryCascadeStep(self.cascadeSteps[stepIndex - 1], 'pressContinuationButton', setStateFunc);
					} else {
						setStateFunc();
					}
				} else if (setStateDoneCallback) {
					setStateDoneCallback();
				}
			};

			// Start setting the cascade state
			setStepState(0);
		},

		validate: function () {
			for (var stepNum = 0; stepNum < this.cascadeSteps.length; stepNum++) {
				// Check if the current cascade step is invalid
				if (!module.queryCascadeStep(this.cascadeSteps[stepNum], 'validate')) {
					return false;
				}
			}

			// All cascade steps are ok
			return true;
		},

		generateJson: function () {
			// Fill the queries array with the relevant steps
			var queries = [];
			var extraInfo = {};

			// Running over all the cascade steps EXCEPT the last one
			for (var stepNum = 0; stepNum < this.cascadeSteps.length - 1; stepNum++) {
				var $cascadeStep = this.cascadeSteps[stepNum];

				// If we should include all the steps, just insert the step query into the query array
				if (this.includeAllSteps) {
					// Insert only non-empty step queries
					var stepQuery = this._getStepQuery($cascadeStep);
					if (Array.isArray(stepQuery)) {
						queries = queries.concat(stepQuery);
					}
					else if (Object.keys(stepQuery).length > 0) {
						queries.push(stepQuery);
					}
				} else { // If we should include all the steps, just insert this step to the extraInfo object
					var name = module.queryCascadeStep($cascadeStep, 'getName');
					extraInfo[name] = module.queryCascadeStep($cascadeStep, 'getVal');
				}
			}

			// Handle the last cascade step
			var $lastCascadeStep = this.cascadeSteps[this.cascadeSteps.length - 1];
			var lastStepQuery = this._getStepQuery($lastCascadeStep);
			if (Array.isArray(lastStepQuery)) {
				queries = queries.concat(lastStepQuery);
			}
			else if (Object.keys(lastStepQuery).length > 0) {

				// If we shouldn't include all the steps, we should insert the extra info to the last step query
				if (!this.includeAllSteps && Object.keys(extraInfo).length > 0) {
					// Remarked because we don't want extraInfo to be added in SessionScreens DSL
					// lastStepQuery[module.queryCascadeStep($lastCascadeStep, 'getName')]['extraInfo'] = extraInfo;
				}

				queries.push(lastStepQuery);
			}

			// In case of nestedPlatform
			if (this.options.property.relation === 'nestedPlatform') {

				// Check if the user chose specific platform
				var selectedPlatform = JSON.parse(module.queryCascadeStep(this.cascadeSteps[0], 'getVal'));
				if (!Array.isArray(selectedPlatform)) {

					// Add the platform to the property name of the query
					var platformName = module.internalUtils.platformsEnum[selectedPlatform];
					const lastStepQuery = queries[queries.length - 1];
					var propertyName = Object.keys(lastStepQuery)[0];
					var nestedPropName = this.options.property.nestedPropName || propertyName;
					lastStepQuery['UserPlatformProperties.' + platformName + '.' + nestedPropName] = lastStepQuery[propertyName];
					delete lastStepQuery[propertyName];

					// Return the json here, if we went to the trouble of computing it
					return lastStepQuery;
				}
			}

			// Determine if the query should be wrapped with relation data (according to the relation defined in
			// the property):
			// 1. parent/child/nested -> Add relation wrapper
			// 2. nestedPlatform or no relation -> NO relation wrapper
			var shouldWrapQueryWithRelation =
                Boolean(this.options.property.relation && this.options.property.relation !== 'nestedPlatform');

			// Build the final query and return it
			var query;
			if (shouldWrapQueryWithRelation) {
				// Wrap the query with relation and property name
				query = module.internalUtils.wrapQueryWithRelation({ 'and': queries }, this.options.property.relation);
				query = module.internalUtils.wrapQueryWithPropertyName(query, this.options.property.name);
			} else if (!this.includeAllSteps) {
				// Get the last one
				query = queries.slice(-1)[0];
			} else {
				query = queries;
			}
			return query;
		},

		hideSteps: function () {
			// Hide all steps
			this._hideSteps(0);
		},

		countFilters: function () {
			var $lastCascadeStep = this.cascadeSteps.slice(-1)[0];
			return module.queryCascadeStep($lastCascadeStep, 'countFilters');
		},

		indentNestedQueryBuilders: function () {
			for (var stepNum = 0; stepNum < this.cascadeSteps.length; stepNum++) {
				module.queryCascadeStep(this.cascadeSteps[stepNum], 'indentNestedQueryBuilders');
			}
		}
	};

	module.QueryCascadeFilter = QueryCascadeFilter;

	return qb;

}($.QueryBuilder || {}));
