import $ from 'jquery';
import './internal-utils';
import './selector';
import './action-selector';
import './query-properties';
import './query-clause-container';
import './value-components';
import './query-builder-parser';
import valueHelper from './value-helper';
import appseeUtils from './appseeUtils';
import appsee from './appsee';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	module.globals = {
		rootElement: null,
		context: null,
		floatsContainer: null,
		dropdownSize: null
	};

	///////////////////
	// Query Builder //
	///////////////////

	var AppseeQueryBuilder = function (element, options) {
		this._init(element, options);
	};

	AppseeQueryBuilder.prototype = {

		_defaultOptions: {
			isRoot: false,
			floatsContainer: 'body', // Container for floating elements
			dropdownSize: 10, // Amount of elements on a dropdown until scroller appear
			properties: [], // A list of objects, of the form {"property": {}}
			maxFilters: 10,
			autoPlatformFilter: true, // should a "Platform: [current platform]" filter automatically be added to the query on init
			emptyNotificationCallback: $.noop
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {

			this.$element = $(element);
			this.options = this._getOptions(options);

			this._addQueryClause = this._addQueryClause.bind(this);
			this._conjunctionChanged = this._conjunctionChanged.bind(this);
			this._clauseRemoved = this._clauseRemoved.bind(this);

			// Only for root element initialization
			if (this.options.isRoot) {
				this.$element.addClass('as-query-builder--root');

				// Several components register to window.onscroll event in order to move floating containers to be aligned
				// with their elements (appseeSelect, daterangepicker, appseeActionSelector). So when the scrolling occures
				// inside the QB we fire scroll event for the window as well
				this.$element.scroll(function () { window.dispatchEvent(new UIEvent('scroll')); });

				module.globals.rootElement = this.$element;

				module.globals.context = this.options.context;

				// Set the container for the floating elements such as dropdowns or datepickers
				if (this.options.floatsContainer) {
					module.globals.floatsContainer = this.options.floatsContainer;
				}

				module.globals.dropdownSize = this.options.dropdownSize;

				this.filtersCountListeners = []; // [{key, parentKey, callback(bool)}]
				this.stateChangedCallbacks = []; // [callback(state)]
			}

			this.propertiesContainer = module.queryProperties(this.options.properties, this.options.isCustomPropertiesSupported);

			this.structure = {
				queryClauses: $('<div/>').addClass('as-query-clause-list')
			};

			this.$loader = $('<div class="as-query-builder-loader" style="display: none;"><div class="ajax-loader-large"/></div>');

			this.$element.empty().addClass('as-query-builder').attr('data-conjunction', 'and')
				.append(this.$loader).append(this.structure.queryClauses);
		},

		_determineContainerState: function (index, propertyIsSelected) {
			var containerState = {
				showAdd: false,
				propertyIsSelected: propertyIsSelected
			};

			if (index === 0) {
				containerState.type = 'root';
				containerState.text = this.options.isRoot ? t('Where') : t('In which');
			} else {
				containerState.text = this.$element.attr('data-conjunction');
				if (index === 1) {
					containerState.type = 'conjunctionMenu';
				} else {
					containerState.type = 'conjunctionText';
					// Capitalize the first letter
					containerState.text = containerState.text.charAt(0).toUpperCase() + containerState.text.slice(1);
				}
			}

			// If the clause is last
			if (index === (this.structure.queryClauses.children().length - 1)) {
				containerState.showAdd = true;
			}

			return containerState;
		},

		_addQueryClause: function (state, clauseInitializedCallback) {
			if (module.appseeQueryBuilder(module.globals.rootElement, 'isMaxFiltersReached')) {
				return;
			}

			var $newClauseContainer = $('<div/>');
			this.structure.queryClauses.append($newClauseContainer);

			module.queryClauseContainer($newClauseContainer, {
				addNewClause: this._addQueryClause,
				clauseRemoved: this._clauseRemoved,
				conjunctionChanged: this._conjunctionChanged,
				state: this._determineContainerState(this.structure.queryClauses.children().length - 1, Boolean(state)),
				clauseOptions: {
					properties: this.propertiesContainer
				}
			});

			// Set state (if given)
			if (state) {
				module.queryClauseContainer($newClauseContainer, 'setClauseState', state, clauseInitializedCallback);
			} else if (clauseInitializedCallback) { // Call the clause initialized callback (if needed)
				clauseInitializedCallback();
			}

			this._changeClausesState();
			module.appseeQueryBuilder(module.globals.rootElement, 'filtersCountChanged');
			module.appseeQueryBuilder(module.globals.rootElement, 'queryBuilderStateChanged');
		},

		_conjunctionChanged: function (operator) {
			this.$element.attr('data-conjunction', operator);
			this._changeClausesState();
		},

		_clauseRemoved: function () {
			if (this.structure.queryClauses.children().length === 0) {
				this.options.emptyNotificationCallback();
				this._addQueryClause();
			}
			this._changeClausesState();
			module.appseeQueryBuilder(module.globals.rootElement, 'filtersCountChanged');
			module.appseeQueryBuilder(module.globals.rootElement, 'queryBuilderStateChanged');
		},

		_changeClausesState: function () {
			var $clauses = this.structure.queryClauses.children();
			for (var i = 0; i < $clauses.length; i++) {
				var $clause = $($clauses[i]);
				module.queryClauseContainer($clause, 'setState', this._determineContainerState(i));
			}
		},

		/******************
         * Public methods *
         ******************/

		initNested: function () {
			if (!this.options.isRoot) {
				// Indent a negative left
				this.indentCurrentNestedQueryBuilder();

				// Set default clauses (if any)
				if (this.options.defaultClauses) {
					for (var i = 0; i < this.options.defaultClauses.length; i++) {
						this._addQueryClause(this.options.defaultClauses[i]);
					}
				}

				// Add an empty clause
				this._addQueryClause();
			}
		},

		validate: function () {
			// Get all the query clauses
			var queryClauses = this.structure.queryClauses.children('.as-clause-container');

			for (var i = 0; i < queryClauses.length; i++) {
				// Check if the current clause is invalid
				if (!module.queryClauseContainer(queryClauses.eq(i), 'validate')) {
					return false;
				}
			}

			// All clauses are ok
			return true;
		},

		generateJson: function () {

			var $queryClauses = this.structure.queryClauses.children('.as-clause-container');

			// Return empty json in case of empty QB
			if ($queryClauses.length === 0) {
				return {};
			}

			var operator = this.$element.attr('data-conjunction');
			var query = {};
			query[operator] = [];
			$queryClauses.each(function () {

				// Don't add empty clauses
				var clauseQuery = module.queryClauseContainer($(this), 'generateJson');
				if (Object.keys(clauseQuery).length > 0) {
					// Flatten the resulting array, if applicable
					if (Array.isArray(clauseQuery)) {
						query[operator] = query[operator].concat(clauseQuery);
					} else {
						query[operator].push(clauseQuery);
					}
				}
			});

			return query;
		},

		isValid: function () {
			return this.validate();
		},

		countFilters: function () {
			return this.structure.queryClauses.children('.as-clause-container').toArray().reduce(function (totalFilters, clause) {
				return totalFilters + module.queryClauseContainer($(clause), 'countFilters');
			}, 0);
		},

		stateSyncError: function (callback) {
			this._stateSyncError = callback;
		},

		stateSyncSuccessful: function (callback) {
			this._stateSyncSuccessful = callback;
		},

		stateSyncDone: function (callback) {
			this._stateSyncDone = callback;
		},

		setState: function (state) {

			if (this.options.isRoot) {
				this.structure.queryClauses.hide();
				this.$loader.show();
			}

			// Remove all the current clauses in the QB
			this.structure.queryClauses.empty();
			this.filtersCountListeners = [];

			var self = this;
			var notifyStateSyncSuccessful = function () {
				// Running this code with setTimeout in order to wait until the page finishes to load all the stylesheet
				// files (which are important for the QB indentation logic)
				setTimeout(function () {

					// Hide the loader + show the clauses (only for the root query builder)
					if (self.options.isRoot) {
						self.$loader.hide();

						// Hide the query builder from the user while indenting ourselves correctly
						self.structure.queryClauses.css('visibility', 'hidden').show();
						self.indentNestedQueryBuilders();
						self.structure.queryClauses.css('visibility', '');

						// New api key, not empty string as default value
						if (appsee.context.apiKey && appsee.context.apiKey.length > 0) {
							// Fire search button click event when queryClauses is initialized and shown, for generate right search query
							self.$element.find('.coordinator-btn-save').click();
						}
					}

					if (self._stateSyncSuccessful) {
						self._stateSyncSuccessful();
					}
				}, 0);
			};

			// Fixed by Lena
			// Set the conjunction
			if (state) {
				this._conjunctionChanged(state.conjuction);
			}

			// Fixed by Lena
			if (!state) {
				state = {};
				state.clauses = [];
			}

			if (state.clauses.length === 0) {
				// Add an empty clause
				this._addQueryClause();

				// Notify that state was synced successfully
				notifyStateSyncSuccessful();
			} else {
				// Add the required clauses to the QB
				var initializedClausesCount = 0;
				for (var i = 0; i < state.clauses.length; i++) {
					this._addQueryClause(state.clauses[i], function () {
						initializedClausesCount++;

						// If we initialized all the clauses, notify that state was synced successfully
						if (initializedClausesCount === state.clauses.length) {
							notifyStateSyncSuccessful();
						}
					});
				}
			}
		},

		getState: function () {
			return JSON.stringify(this.generateJson());
		},

		registerFiltersCountListener: function (key, parentKey, callback) {
			if (this.options.isRoot && !this.filtersCountListeners.find(function (item) { return item.key === key; })) {
				var listener = { key: key, callback: callback };

				if (parentKey) {
					listener.parentKey = parentKey;
				}

				this.filtersCountListeners.push(listener);
			}
		},

		unregisterFiltersCountListener: function (key) {
			// If item exists
			if (this.options.isRoot) {

				// Find the key and its descendants, the array is ordered so the parent will always be before children
				var keysToRemove = [];
				this.filtersCountListeners.forEach(function (item) {
					if (item.key === key) {
						keysToRemove.push(item.key);
					} else if (keysToRemove.includes(item.parentKey)) {
						keysToRemove.push(item.key);
					}
				});

				if (keysToRemove.length > 0) {

					// Construct new array from the old one without the keys to remove
					this.filtersCountListeners = this.filtersCountListeners.filter(function (item) {
						return !keysToRemove.includes(item.key);
					});
				}
			}
		},

		isMaxFiltersReached: function () {
			if (this.options.isRoot) {
				return this.countFilters() >= this.options.maxFilters;
			} else {
				return false;
			}
		},

		filtersCountChanged: function () {
			if (this.options.isRoot) {
				var maxFiltersReached = this.isMaxFiltersReached();

				// Notify listeners
				this.filtersCountListeners.forEach(function (item) {
					item.callback(maxFiltersReached);
				});
			}
		},

		getProperties: function () {
			return this.propertiesContainer;
		},

		indentCurrentNestedQueryBuilder: function () {

			// Needed only for nested query builders
			if (!this.options.isRoot) {
				var $break = $('<div style="flex-basis: 100%; height: 0;"></div>');
				$break.insertBefore(this.$element.parent('.as-query-property-filter'));
			}
		},

		indentNestedQueryBuilders: function () {

			// Indent myself
			this.indentCurrentNestedQueryBuilder();

			// Get all the query clauses
			var queryClauses = this.structure.queryClauses.children('.as-clause-container');

			// Indent all the inner query builders recursively
			for (var i = 0; i < queryClauses.length; i++) {
				module.queryClauseContainer(queryClauses.eq(i), 'indentNestedQueryBuilders');
			}
		},

		registerStateChangedCallback: function (callback) {
			this.stateChangedCallbacks.push(callback);
		},

		queryBuilderStateChanged: function () {

			// Check that the qb is valid
			if (this.isValid()) {

				// Notify listeners
				var self = this;
				this.stateChangedCallbacks.forEach(function (callback) {
					callback(self.getState());
				});
			}
		},

		updateContext: function (property, newValue) {
			// If the new value is null, we should delete the property.
			// Otherwise, update the property value.
			if (newValue === null) {
				delete module.globals.context[property];
			} else {
				module.globals.context[property] = newValue;
			}
		}
	};

	// Use this method internally
	module.appseeQueryBuilder = function (elements, option) {
		var args = Array.prototype.slice.call(arguments).splice(1);
		return appseeUtils.createClassInstance(elements, 'as-query-builder', AppseeQueryBuilder, option, args);
	};

	// Use this plug-in only externally
	// module.appseeQueryBuilder2 = function (elements, option) {
	// 	// if option is string, that means a function needs to be called
	// 	if (typeof option !== 'string') {
	//
	// 		// force isRoot to be true from the outside when initiating the plug-in
	// 		if ($.isEmptyObject(option)) {
	// 			option = {};
	// 		}
	// 		option.isRoot = true;
	// 	}
	//
	// 	// pass arguments to appseeQueryBuilder seperatly as if it was called directly
	// 	var args = [elements, option];
	// 	[].push.apply(args, Array.prototype.slice.call(arguments).splice(1));
	// 	return module.appseeQueryBuilder.apply(null, args);
	// };

	$.fn.appseeQueryBuilder = function (option) {
		// if option is string, that means a function needs to be called
		if (typeof option !== 'string') {

			// force isRoot to be true from the outside when initiating the plug-in
			if ($.isEmptyObject(option)) {
				option = {};
			}
			option.isRoot = true;
		}

		// pass arguments to appseeQueryBuilder seperatly as if it was called directly
		var args = [this, option];
		[].push.apply(args, Array.prototype.slice.call(arguments).splice(1));
		return module.appseeQueryBuilder.apply(null, args);
	};

	qb.utils = {
		valueComponents: module.valueComponents,
		valueHelper: valueHelper,
		module: module
	};

	return qb;

}($.QueryBuilder || {}));


