import $ from 'jquery';
/** Attribute with Prefix **/
;(function ($) {
	// Returns an object of all the HTML attributes starts with a given prefix.
	//
	$.fn.getAttributesWithPrefix = function (prefix, crop, hyphenToCamelCase) {
		var result = {};

		this.each(function () {
			$.each(this.attributes, function () {
				if (!prefix || this.name.indexOf(prefix) === 0) {
					var name = this.name;
					if (prefix && crop) {
						name = name.substring(prefix.length);
					}
					if (hyphenToCamelCase) {
						name = name.replace(/-([a-z])/g, function(g) { return g[1].toUpperCase(); });
					}
					result[name] = this.value;
				}
			});
		});
		return result;
	};
}($));
