import $ from 'jquery';
import appseeUtils from './appseeUtils';
import './appsee-mark-exclusively';
/** Appsee Actions Grid
    Depends on: markExclusively **/

;(function ($) {
	// Actions grid for an action type. Use the 'addActionsToGrid' method in order to populate the grid.

	var ActionsGrid = function (element, options) {
		this.init(element, options);
	};

	ActionsGrid.prototype = {
		NAVIGATION_BACK_TYPE: 6,

		defaults: {
			actionSelectedCallback: $.noop,
			actionNotSelectedCallback: $.noop,
			spaceItemsAround: false
		},

		getOptions: function (options) {
			return $.extend({}, this.defaults, this.$element.data(), options);
		},

		init: function (element, options) {
			this.$element = $(element).addClass('actions-grid');
			this.options = this.getOptions(options);

			this.selectAction = this.selectAction.bind(this);

			if (this.options.spaceItemsAround) {
				this.$element.css('justify-content', 'space-around');
			}

			this.$actionItemTemplate = $('<div class="action-item" action-key="">' +
                '<div class="action-item__multiline"><p class="action-item__content" /></div></div>');
		},

		selectAction: function (actionKey, userInitiated) {
			this.actionSelected(this.$element.find(`.action-item[action-key="${actionKey}"]`), userInitiated);
		},

		// Auto select action if only one exists
		handleSingleAction: function (actionType) {
			var $gridItems = this.$element.find('.action-item');
			if ($gridItems.length === 1) {
				this.actionSelected($gridItems[0]);

				if (actionType === this.NAVIGATION_BACK_TYPE) {
					this.$element.hide();
				}
			}
		},

		actionSelected: function (actionSelector, userInitiated) {
			$(actionSelector).markExclusively('selected');
			this.options.actionSelectedCallback(userInitiated);
		},

		getSelectedAction: function () {
			var $action = this.$element.find('.action-item.selected');
			if ($action.length) {
				return $action.data('as-actionItem');
			} else {
				return null;
			}
		},

		addActionsToGrid: function (actions) {
			var self = this;

			for (var i = 0; i < actions.length; i++) {
				// Create an HTML representation for each action
				var currAction = actions[i];

				if (currAction.ControlDescription !== null) {
					var $gridItem = this.$actionItemTemplate.clone();
					$gridItem.attr('action-key', currAction.ActionKey);
					$gridItem.attr('action-desc', currAction.ActionDescription);
					$gridItem.attr('title', appseeUtils.stripHtmlTags(currAction.ControlDescription));

					$gridItem.find('.action-item__content').html(currAction.ControlDescription);
					if (currAction.AdditionalInfo) {
						$gridItem.find('.action-item__content').attr('data-action-info', currAction.AdditionalInfo);
					}

					$gridItem.click(function (e) {
						self.actionSelected($(this), true);
					});

					$gridItem.data('as-actionItem', currAction);

					this.$element.append($gridItem);
				}
			}
			appseeUtils.prepareScreenActionHovers();
		},

		clearPresentedActions: function () {
			this.$element.empty();
			this.$element.show();
			this.options.actionNotSelectedCallback();
		}
	};

	$.fn.appseeActionsGrid = function (option) {
		var args = Array.prototype.slice.call(arguments);
		return appseeUtils.createClassInstance(this, 'as-actionsGrid', ActionsGrid, option, args);
	};
})($);
