import $ from 'jquery';
import appseeUtils from './appseeUtils';
import './appsee-attributes-with-prefix';
import './bootstrap-select';
import { t } from 'sn-translate';

/** Appsee Select
    Depends on: getAttributesWithPrefix **/
;(function ($) {
	// An extension of "bootstrap-select" plugin, with group selection and select/deselect all
	//
	// Settings:
	//   Boolean selectAllEnabled - Adds buttons of select/deselect all.
	//   Boolean selectGroupEnabled - Add group selection checkboxes.
	//   Object/List items - Initiate the select with given items (Object: {option.val: option.text}, List: [option.val (the item's index) / option.text]).
	//   String fetchUrl - Populate the select with items received from the given fetchUrl.
	$.fn.appseeSelect = function (options) {
		var args = Array.prototype.slice.call(arguments);

		return this.each(function() { // For each selected element
			var component = $(this).addClass('as-select');

			var state = component.data('as-select');

			// The container of the select element and the bootstrap implementation of the select
			var $selectContainer;

			if (state) {
				$selectContainer = component.parent();
			}

			var registerSearchboxEvents = function () {
				var $searchbox = state.dropdownElement.find('.bs-searchbox input.form-control');

				$searchbox.addClass('m-wrap');

				// When container is set, when pressing enter the dropdown does not closed by bootstrap
				// This will close the dropdown container
				if (state.settings.container) {
					$searchbox.keyup(function (e) {
						if (e.keyCode === 13) {
							closeAll();
						}
					});
				}
			};

			/************************
             *  Select/desesect all
             ************************/

			var markSelectAllCheckbox = function (selected, $selectAllDivParam) {
				var $selectAllDiv = $selectAllDivParam == null ? state.dropdownElement.find('div.as-select-all-cb') : $selectAllDivParam;
				var $selectAllDivText = $selectAllDiv.find('span');
				if (selected) {
					state.dropdownElement.find('div.as-select-all-cb').addClass('all-selected');
					$selectAllDivText.text(t('deselect all'));
				} else {
					state.dropdownElement.find('div.as-select-all-cb').removeClass('all-selected');
					$selectAllDivText.text(t('select all'));
				}
			};

			// Select all elements,       selected === true
			// Deselect all elements,     selected === false
			var addSelectAllCheckbox = function () {

				// Don't add the check box if there are no items
				if (state.dropdownElement.find('ul.dropdown-menu').length === 0)
					return;

				var $selectAllDivText = $('<span />').css({ 'margin-right': '5px', 'font-size': '12px' }).text(t('select all'));

				var $selectAllDiv = $('<div />').addClass('as-select-all-cb')
					.append($selectAllDivText)
					.append('<i class="glyphicon glyphicon-ok icon-ok check-mark"></i>');

				// SelectAll div click event
				$selectAllDiv.click(function(e) {
					var $groupSelectionCheckboxes = state.dropdownElement.find('div.as-select-group-cb');

					if ($(this).hasClass('all-selected')) {
						component.appseeSelect('deselectAll');
						$groupSelectionCheckboxes.removeClass('group-selected');
						markSelectAllCheckbox(false);
					} else {
						component.appseeSelect('selectAll');
						$groupSelectionCheckboxes.addClass('group-selected');
						markSelectAllCheckbox(true);
					}

					// Prevent closing dropdown
					e.stopPropagation();
				});

				var $selectAllDivWrapper = $('<div />').addClass('as-select-all-wrapper').append($selectAllDiv);
				state.dropdownElement.find('ul.dropdown-menu').before($selectAllDivWrapper);
			};

			/********************
             *  Group Selection
             ********************/

			// Given the group header, returns a selector of all the group option elements
			var getGroupElements = function ($groupHeader) {
				var optgroup = $groupHeader.attr('data-optgroup');
				return component.find('optgroup[data-optgroup=' + optgroup + ']').children('option');
			};

			// Select all group elements,       selected == true
			// Deselect all group elements,     selected == false
			var markGroupElements = function ($groupHeader, selected) {
				var currentSelectedElementVals = component.val();
				currentSelectedElementVals = currentSelectedElementVals || [];
				var groupElementVals = getGroupElements($groupHeader).map(function () { return $(this).val(); }).get();
				groupElementVals = groupElementVals || [];
				if (selected) {
					component.val(currentSelectedElementVals.concat(groupElementVals));
					component.selectpicker('render');
					$groupHeader.find('div.as-select-group-cb').addClass('group-selected');
				} else {
					component.val(currentSelectedElementVals.filter(function (e) { return groupElementVals.indexOf(e) === -1; }));
					component.selectpicker('render');
					$groupHeader.find('div.as-select-group-cb').removeClass('group-selected');
				}
				var shouldSelectAll = state.dropdownElement.find('ul.dropdown-menu li[data-original-index]:not(.selected)').length === 0;
				markSelectAllCheckbox(shouldSelectAll);
			};

			// Add SelectGroup checkboxes on all of the headers
			var addSelectGroupCheckboxes = function() {
				var $selectGroupDiv = $('<div />').addClass('as-select-group-cb')
					.append('<i class="glyphicon glyphicon-ok icon-ok check-mark"></i>');

				// SelectGroup div click event
				$selectGroupDiv.click(function(e) {
					var $groupHeader = $(this).closest('.dropdown-header');
					if ($(this).hasClass('group-selected')) {
						markGroupElements($groupHeader, false);
					} else {
						markGroupElements($groupHeader, true);
					}

					// Prevent closing dropdown
					e.stopPropagation();
				});

				var $groupHeaders = state.dropdownElement.find('ul.dropdown-menu .dropdown-header');
				$groupHeaders.append($selectGroupDiv);
			};

			/***********
             *  Values
             ***********/

			var addItems = function (items) {
				if (!state.settings.fixedOptions) {
					component.empty();
				} else {
					$(component).find('option').each(function(index, option) {
						// Used for example to leave 'all' and 'manage versions' values inside the dropdown
						// Used like this:
						// fixedOptions: [
						//     'all',
						//      ....
						// ],
						if (!state.settings.fixedOptions.includes($(option).attr('value'))) {
							$(option).remove();
						}
					});
				}

				var itemValueAccessor = getItemValueAccessor(items);

				for (var item in items) {
					if (items.hasOwnProperty(item)) {
						var $option = $('<option />');
						var itemValue = itemValueAccessor(item);
						var itemText = items[item];
						// For special sorted cases
						if (itemText.Key != null && itemText.Value != null) {
							itemValue = itemText.Key;
							itemText = itemText.Value;
						}

						if (state.settings.dataContentMapping) {
							// Provide an icon to the option
							var iconName = state.settings.dataContentMapping && state.settings.dataContentMapping[itemValue];
							if (iconName) {
								$option.attr('data-content', '<div class=\'option-icon-container\'><i class=\'icon ' + iconName + '\'></i></div><div class=\'option-text-container\'> ' + itemText + '</div>');
							}
						}

						component.append($option.val(itemValue).text(itemText));
					}
				}
			};

			var addGroupedItems = function (groupsDictionary) {
				component.empty();

				if (Object.keys(groupsDictionary).length === 1) {

					// In case of single group, we should add the items as regular items so we "fetch" the single group items
					// from the nested groups dictionary and add it to the select
					var items = Object.values(groupsDictionary)[0];
					addItems(items);
				} else {

					var groupIndex = 1;

					// For each group
					for (var groupLabel in groupsDictionary) {
						if (groupsDictionary.hasOwnProperty(groupLabel)) {

							// Create the group element
							var $currGroupElement = $('<optgroup data-optgroup="' + groupIndex + '" label="' + groupLabel + '">');
							var currGroupItems = groupsDictionary[groupLabel];

							var itemValueAccessor = getItemValueAccessor(currGroupItems);

							// Append all the group items to the group element
							for (var item in currGroupItems) {
								if (currGroupItems.hasOwnProperty(item)) {
									$currGroupElement.append($('<option value="' + itemValueAccessor(item) + '">' +
                                        currGroupItems[item] + '</option>'));
								}
							}

							// Add the group element to the select component
							component.append($currGroupElement);
							groupIndex++;
						}
					}
				}
			};

			/******************
             *  Load from URL
             ******************/

			var addItemsFromUrlAsync = function (fetchUrl) {
				if (!fetchUrl)
					return;

				var title = component.attr('title');
				component.attr('title', 'Loading...');
				component.prop('disabled', true);
				component.selectpicker('refresh');

				$selectContainer.children('button').append($('<div/>').addClass('as-select-loader'));

				var successFunc = function (data) {
					if (data != null) {
						component.empty();
						addItems(data);
						component.attr('title', title);
						component.prop('disabled', false);
						component.appseeSelect('refresh');
						$selectContainer.children('button').children('.as-select-loader').remove();
					}
				};

				var errorFunc = function () {
					component.attr('title', title);
					component.prop('disabled', false);
					component.appseeSelect('refresh');
					$selectContainer.children('button').children('.as-select-loader').remove();
				};

				// Load values
				appsee.loadPart(fetchUrl, successFunc, {}, errorFunc);
			};

			/********************/

			// Render the selection checkboxes (groups & all)
			var renderSelectionCheckboxes = function() {
				var $liItems = state.dropdownElement.find('ul.dropdown-menu li:not(.divider)');
				var shouldSelectAll = true;
				var $currGroupHeader = null;

				$liItems.each(function () {
					var $this = $(this);
					if ($this.hasClass('dropdown-header')) {
						$currGroupHeader = $this;
						$currGroupHeader.find('.as-select-group-cb').addClass('group-selected');

					} else if (!$this.hasClass('selected')) {
						if ($currGroupHeader != null)
							$currGroupHeader.find('.as-select-group-cb').removeClass('group-selected');
						shouldSelectAll = false;
					}
				});

				markSelectAllCheckbox(shouldSelectAll);
			};

			// Close the dropdown and mark the select container closed as well
			var closeAll = function () {
				$selectContainer.removeClass('open');
				state.dropdownElement.parent().removeClass('open');
			};

			// Actions
			var init = function () {
				// Default flag value set as true
				state.shouldTriggerChangeEvent = true;

				// Initiate the items if given
				if (state.settings.items) {
					addItems(state.settings.items);
				}

				// Init selectpicker
				// Remarked by Lena
				component.selectpicker(state.settings);

				$selectContainer = component.parent();

				// This variable will hold the dropdown element that bootstrap implemented.
				// When the dropdown is initiated it is nested directly inside the $selectContainer.
				// If it has a different container then on the first time the dropdown is opened it going to be relocationed to the container DOM under ".bsContainer" element.
				state.dropdownElement = $selectContainer.children('div.dropdown-menu');

				state.uid = appseeUtils.guid();

				if (state.settings.fetchUrl) addItemsFromUrlAsync(state.settings.fetchUrl);
				if (state.settings.selectGroupEnabled) addSelectGroupCheckboxes();
				if (state.settings.selectAllEnabled) addSelectAllCheckbox();

				// If provided, add a class to the real bootstrap select html element (The one the plugin builds)
				// Also change select styling to match the facelift required changes
				if (state.settings.classExtension) {
					$selectContainer.addClass(state.settings.classExtension);
					$selectContainer.find('.dropdown-toggle').css('outline', 'none !important');
				}

				// If provided, apply opened dropdown style
				if (state.settings.dropdownStyle) {
					$selectContainer.find('.dropdown-menu').css(state.settings.dropdownStyle);
				}

				// Replace bootstrap down arrow icon with ours
				if (state.settings.replaceCaret) {
					$selectContainer.find('.caret').remove();
					$selectContainer.find('.bs-caret').append('<now-icon icon="chevron-down-fill" class="select-icon" size="md"/>');
				}

				registerSearchboxEvents();

				// When container is set we need to handle manually the closing of the dropdown container
				// because the bootstrap version is not compatible with the minimum requirement of bootstrap-select
				if (state.settings.container) {

					// How to stop click event from mouseup event - https://stackoverflow.com/a/20290312
					// This method stops click event from bubbling and remove itself from the element
					var stopButtonClick = function (e) {
						e.preventDefault();
						e.stopPropagation();
						this.removeEventListener('click', stopButtonClick, true);
					};

					// Register the mouseup event under appseeselect namespace with the state uid so it can be removed later
					// It's a must because bootstrap-select have numerous bugs regarding closing the dropdown selection
					$(document).on('mouseup.appsee.select.' + state.uid, function (e) {
						// Only if the dropdown is open
						if ($selectContainer.hasClass('open') || state.dropdownElement.parent().hasClass('open')) {

							// If dropdown is open, and click was on the select (bootstrat-select button),
							// we need to close the dropdown and stop the event from opening it again
							if (appseeUtils.isClickOnElement(e, $selectContainer)) {
								$selectContainer[0].addEventListener(
									'click',
									stopButtonClick,
									true // This registeres this listener for the capture phase instead of the bubbling phase!
								);
							}

							// If there is a header and the click was on the close button we want to close the container
							// and stop the button propogation because bootstrap will open it again
							var $closeButton = state.dropdownElement.find('.popover-title .close');
							var isClickOnCloseButton = appseeUtils.isClickOnElement(e, $closeButton);
							if (isClickOnCloseButton) {
								state.dropdownElement[0].addEventListener(
									'click',
									stopButtonClick,
									true // This registeres this listener for the capture phase instead of the bubbling phase!
								);
							}

							// If there is a done button and the click was on the done button we want to close the container
							// and stop the button propogation because bootstrap will open it again
							var $doneButton = state.dropdownElement.find('.bs-donebutton .btn');
							var isClickOnDoneButton = appseeUtils.isClickOnElement(e, $doneButton);
							if (isClickOnDoneButton) {
								state.dropdownElement[0].addEventListener(
									'click',
									stopButtonClick,
									true // This registeres this listener for the capture phase instead of the bubbling phase!
								);
							}

							// If it's multiple select and the click was inside the selection dropdown
							// but not on the close button nor the done button, we want to keep the container open
							if (state.settings.selectAllEnabled && !isClickOnCloseButton && !isClickOnDoneButton &&
                                appseeUtils.isClickOnElement(e, state.dropdownElement)) {
								return;
							}

							// If click was on the searchbox we want to keep the container open
							var $searchbox = state.dropdownElement.find('.bs-searchbox');
							if (appseeUtils.isClickOnElement(e, $searchbox)) {
								return;
							}

							// If click was on the inner dropdown element it was the scroll bar
							// (every other place is the "li"s / searchbox / header) so we want to keep the container open
							if (appseeUtils.isClickOnElement(e, state.dropdownElement.children('.dropdown-menu.inner'), false)) {
								return;
							}

							// Close the dropdown and mark the select container closed as well
							closeAll();

							component.trigger('hide.appsee.select', e);
						}
					});

					// When container is set, when pressing enter the dropdown does not closed by bootstrap
					// This will close the dropdown container
					state.dropdownElement.keyup(function (e) {
						if (e.keyCode === 13) {
							closeAll();
						}
					});

					// Handle component deletion from DOM
					component.on('remove', function () {

						// Not stucking the UI thread
						setTimeout(function () {

							// Remove redundant handler from document
							$(document).off('.appsee.select.' + state.uid);

							// Remove the dropdown container from it's parent DOM
							state.dropdownElement.parent().remove();
						}, 0);
					});
				}

				component.on('change', function (e) {
					renderSelectionCheckboxes();

					// Supporting onChange callback function to pass as part of the plugin options
					// If "setValueWithoutTriggeringChange" function was called we don't want to trigger change
					if (state.shouldTriggerChangeEvent) {
						state.settings.onChange(e);
					}
				});
			};

			var refresh = function () {
				component.selectpicker('refresh');

				// The selectpicker('refresh') creates the elements all over again,
				// thus the SelectGroup checkboxes have to be initialized
				if (state.settings.selectGroupEnabled) {
					addSelectGroupCheckboxes();
					renderSelectionCheckboxes();
				}

				// If there are no elements in the dropdown, hide SelectAll checkbox
				if (state.dropdownElement.find('ul.dropdown-menu li').length === 0)
					state.dropdownElement.find('.as-select-all-wrapper').hide();
				else
					state.dropdownElement.find('.as-select-all-wrapper').show();
			};

			// Support an option to set select value without triggering onChange function
			var setValueWithoutTriggeringChange = function(value) {
				state.shouldTriggerChangeEvent = false;
				component.appseeSelect('val', value);
				state.shouldTriggerChangeEvent = true;
			};

			// Loading
			var showLoading = function () {
				$selectContainer.children('button').find('span.bs-caret').hide();
				$selectContainer.children('button').append('<div style="display: inline-block;" class="ajax-loader-small"></div>');
			};

			var hideLoading = function () {
				$selectContainer.children('button').find('span.bs-caret').show();
				$selectContainer.children('button').find('.ajax-loader-small').remove();
			};

			var destroy = function() {
				component.selectpicker('destroy').remove();
			};

			var hideItem = function(value) {
				var optionQuery = 'option[value=' + value + ']';
				var $item = component.find(optionQuery).hide();

				// If the item is select, deselect it
				if ($item.is(':selected')) {
					$item.attr('selected', false);

					// Select an other item (first one) if exists
					var $itemToSelect = component.find(':not(' + optionQuery + '):first');
					if ($itemToSelect.length) {
						setValueWithoutTriggeringChange($itemToSelect.val());
					}
				}

				refresh();
			};

			var showItem = function(value) {
				component.find('option[value=' + value + ']').css('display', 'flex');
				refresh();
			};

			var val = function(value) {
				component.selectpicker('val', value);
				component.appseeSelect('refresh');
			};

			var getItemValueAccessor = function (items) {
				var transformer = state.settings.valueTransformer || function (v) { return v; };
				return Array.isArray(items) ?
					function (x) { return transformer(items[x]); } :
					function (x) { return transformer(x); };
			};

			// Functions of the dropdown
			var funcs = {
				refresh: refresh,
				showLoading: showLoading,
				hideLoading: hideLoading,
				fetch: addItemsFromUrlAsync,
				destroy: destroy,
				addItems: addItems,
				addGroupedItems: addGroupedItems,
				setValueWithoutTriggeringChange: setValueWithoutTriggeringChange,
				hideItem: hideItem,
				showItem: showItem,
				val: val
			};

			// Function call
			if (typeof options === 'string') {
				if (!state)
					return null;

				// Perform action/function if string given instead of settings object, end return
				if (options in funcs) {
					return funcs[options].apply(component, args.slice(1));
				} else {
					return component.selectpicker.apply(component, args);
				}
			}

			/*******************
             *  Initialization
             *******************/

			// In case we're changing options dynamically we don't want to return the component
			// as it is, but add/remove new options and refresh
			if (state)
				return component;

			var settingsAttributes = $.extend(component.getAttributesWithPrefix('data-', true, true), options);

			state = {
				settings: $.extend({
					selectAllEnabled: component.attr('multiple') != null,
					selectGroupEnabled: true,
					items: null,
					fetchUrl: null,
					selectOnTab: true,
					tickIcon: 'icon-ok',
					size: 10,
					onChange: $.noop,
					dropupAuto: false,
					fixedOptions: [],
					valueTransformer: null,
					dataContentMapping: {},
				}, settingsAttributes)
			};

			init();

			// Load setting to state variable
			component.data('as-select', state);

			return component;
		});
	};
})($);
