import validators from './validators';
import appseeUtils from './appseeUtils';
import $ from 'jquery';
import { t } from 'sn-translate';

import './internal-utils';
import './selector';
import './components';
import './query-builder-filter';
import './query-term-filter';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryCascadeStep = function (element, options) {
		this._init(element, options);
	};

	QueryCascadeStep.prototype = {

		_defaultOptions: {},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			var self = this;
			this.$element = $(element);

			this.options = this._getOptions(options);
			this.property = this.options.property;
			this.changeCallback = function (name, val) { return; };

			this.skipValidation = false;

			// Add step component div to element
			this.stepComponent = $('<div/>').addClass('as-query-cascade-step-component').attr('name', this.property.name);
			this.$element.append(this.stepComponent);

			// Add continue button if needed
			if (this.property.continueBtnLabel) {

				// Create the continue button
				var $continueBtn = $('<a/>').addClass('as-query-cascade-continue-btn appsee-link').append(this.property.continueBtnLabel).click(function () {
					self._continueButtonPressed();
				});
				$continueBtn.hide();
				this.continueBtn = $continueBtn;
				this.$element.append($continueBtn);
			}

			// Init step according to the component
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {    // Step is a query builder
				this.skipValidation = true;

				// Initialize the query builder filter
				var queryBuilderFilterOptions = { property: this.property };
				if (this.options.hideOptionalStepCallback) {
					queryBuilderFilterOptions['emptyNotificationCallback'] = this.options.hideOptionalStepCallback;
				}
				module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, queryBuilderFilterOptions);

			} else if (this.property.getPropertyFilter() === module.QueryCascadeFilter) {  // Step is a cascade in itself
				module.queryPropertyFilter(this.stepComponent, module.QueryCascadeFilter, this.options);

			} else if (this.property.type) {  // Step is term filter
				this.skipValidation = true;

				// Initialize the property filter component
				module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter,
					{ property: this.property, changeCallback: this._internalChangeCallbackFunc.bind(this) });

			} else {    // Step is selector
				module.selector(this.stepComponent, {
					description: 'Select ' + this.property.description,
					noValuesText: '(No ' + this.property.pluralDescription + ')',
					changeCallback: this._internalChangeCallbackFunc.bind(this)
				});
			}
		},

		_continueButtonPressed: function (nextStepShownCallback) {
			var val = this.getVal();
			val = Array.isArray(val) ? val[0] : val;
			this.continueBtn.hide();
			this._callChangeCallback(val, nextStepShownCallback);
		},

		_internalChangeCallbackFunc: function (val, nextStepShownCallback) {

			if (!this.skipValidation) {
				module.components.validateValueComponent(val, 'not-empty', this.stepComponent);
			}

			// Hide the next steps + the continue button (at this point, we still didn't pass the continuation check)
			if (this.property.continueBtnLabel) {
				this._callChangeCallback(null);
				this.continueBtn.hide();
			}

			// Check if we have continuation check method and it returned false
			if (this.property.continuationCheckMethod && !this.property.continuationCheckMethod(val, this.extraData)) {
				this._callChangeCallback(null, nextStepShownCallback);
				return;
			}

			// We passed the continuation check, then we show the continue button or fire the change callback
			if (this.property.continueBtnLabel) {

				// Show the continue button only when one value is selected
				if (!Array.isArray(val) || val.length === 1) {
					this.continueBtn.show();
				}

				if (nextStepShownCallback) {
					nextStepShownCallback();
				}
			} else {
				this._callChangeCallback(val, nextStepShownCallback);
			}
		},

		_callChangeCallback: function (value, nextStepShownCallback) {
			var stepName = this.property.contextBindedPropName || this.getName();
			this.changeCallback(stepName, value, nextStepShownCallback);
		},

		_fetch: function (fetchParams, valuesFetchedCallback) {
			var paramsStr = '';
			if (fetchParams) {
				var objectKeys = Object.keys(fetchParams);
				if (objectKeys.length) {
					var seperator = this.property.fetchValuesUrl && this.property.fetchValuesUrl.includes('?') ? '&' : '?';
					paramsStr = seperator + objectKeys.map(function (key) { return key + '=' + fetchParams[key]; }).join('&');
				}
			}

			var self = this;
			var valuesFetched = function (values) {

				var setDefaultValueFunc = function () {

					// Set default value (if exists)
					var defaultValue = self.property.cascadeStepDefaultValue;
					if (defaultValue !== undefined) {
						self.setVal(defaultValue);
					} else if (values && Object.keys(values).length === 1) {
						// In case of one value, we set it
						self.setVal(Object.keys(values)[0]);
						self.disableStep();
					}

					// If some value is selected (default value, single value, etc), we call the internal
					// change callback function in order to simulate the required behavior
					var selectedVal = self.getVal();
					if (validators['value-selected'].accessor(selectedVal)) {
						self._internalChangeCallbackFunc(selectedVal, valuesFetchedCallback);
					} else if (valuesFetchedCallback) {
						valuesFetchedCallback();
					}
				};

				// Fetch extra data (if needed) and then set default value
				if (self.property.fetchExtraDataUrl) {
					self._fetchExtraData(self.property.fetchExtraDataUrl, setDefaultValueFunc);
				} else {
					setDefaultValueFunc();
				}
			};

			// Add help text (if needed)
			if (this.property.helpText) {
				this.$element.prepend('<div class="as-help-text as-help-text--space-left-1x as-help-text--space-right-2x">' + this.property.helpText + '</div>');
			}

			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {

				// Async load of properties to the object filter according to the fetch params
				module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, 'fetchPropertiesAndRender', fetchParams, valuesFetched);

			} else if (this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				module.queryPropertyFilter(this.stepComponent, module.QueryCascadeFilter, 'render', valuesFetched);

			} else if (this.property.type) {
				if (this.property.fetchValuesUrl) {
					module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'setCustomFetchValuesUrl', this.property.fetchValuesUrl + paramsStr);
				}
				module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'render', null, valuesFetched);

			} else {
				module.selector(this.stepComponent, 'fetch', this.property.fetchValuesUrl + paramsStr, valuesFetched);
			}
		},

		_fetchExtraData: function (fetchUrl, extraDataFetchedCallback) {

			var self = this;
			var successFunc = function (data) {
				if (data != null) {
					self.extraData = data;
					if (extraDataFetchedCallback) {
						extraDataFetchedCallback();
					}
				}
			};

			var errorFunc = function (data) {
				self.$element.empty();
				self.$element.append('<span class="as-query-fetch-error-msg">' + t('Error fetching actions') + '<span/>');
			};

			// Fetch extra data
			module.internalUtils.performAjaxCall(fetchUrl, successFunc, errorFunc);
		},

		/******************
         * Public methods *
         ******************/

		pressContinuationButton: function (nextStepShownCallback) {
			this._continueButtonPressed(nextStepShownCallback);
		},

		cancelContinuation: function () {
			this._callChangeCallback(null);
			this.continueBtn.show();
		},

		registerChangeEvent: function (callback) {
			this.changeCallback = callback;
		},

		disableStep: function () {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter || this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				this.stepComponent.empty();
			} else if (this.property.type) {
				module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'setDisabled', true);
			} else {
				module.selector(this.stepComponent, 'setDisabled', true);
			}
		},

		hideStep: function () {
			this.$element.children('.as-help-text').remove();
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {
				this.stepComponent.empty();
			} else if (this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				module.queryPropertyFilter(this.stepComponent, module.QueryCascadeFilter, 'hideSteps');
			} else if (this.property.type) {
				this.stepComponent.empty();
			} else {
				module.selector(this.stepComponent, 'clear', true);
			}
		},

		showStep: function (fetchParams, stepShownCallback) {
			this._fetch(fetchParams, stepShownCallback);
		},

		getName: function () {
			return this.property.name;
		},

		getVal: function () {
			var val;

			if (this.property.getPropertyFilter() === module.QueryBuilderFilter || this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				val = null;
			} else if (this.property.type) {
				val = module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'getValue');
			} else {
				val = module.selector(this.stepComponent, 'getVal');
			}

			if (this.property.valueTransformer) {
				val = this.property.valueTransformer(val);
			}

			return val;
		},

		setVal: function (value, callChangeCallback, nextStepShownCallback) {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter || this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				return;
			} else if (this.property.type) {
				module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'setValue', value);
			} else {
				module.selector(this.stepComponent, 'setVal', value);
			}
			if (callChangeCallback) {
				this._callChangeCallback(value, nextStepShownCallback);
			}
		},

		setState: function (state, setStateDoneCallback) {

			var self = this;
			var stepStateSetCallback = function () {
				self._internalChangeCallbackFunc(self.getVal(), setStateDoneCallback);
			};

			// Set state according to the state object
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {
				module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, 'setState',
					state, stepStateSetCallback);
			} else if (this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				module.queryPropertyFilter(this.stepComponent, module.QueryCascadeFilter, 'setState',
					state, stepStateSetCallback);
			} else if (this.property.type) {
				module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'setState',
					state, stepStateSetCallback);
			} else {
				module.selector(this.stepComponent, 'setVal', state['value']);
				stepStateSetCallback();
			}
		},

		validate: function () {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {
				return module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, 'validate');
			} else if (this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				return module.queryPropertyFilter(this.stepComponent, module.QueryCascadeFilter, 'validate');
			} else if (this.property.type) {
				// TBD to Lena - must to be fixed - platform hidden seismic project bug
				if (this.property.type === 'hidden') {
					return true;
				}
				return module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'validate');
			} else {
				// Appsee selector
				// validate with message because cascade cannot be not completed
				var value = module.selector(this.stepComponent, 'getVal');
				module.components.validateValueComponent(value, 'not-empty', this.stepComponent);
				return Boolean(value);
			}
		},

		jsonContainsPropertyName: function () {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter || this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				return true;
			} else if (this.property.type) {
				return true;
			} else {
				return false;
			}
		},

		generateJson: function () {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {
				return module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, 'generateJson');
			} else if (this.property.getPropertyFilter() === module.QueryCascadeFilter) {
				return module.queryPropertyFilter(this.stepComponent, module.QueryCascadeFilter, 'generateJson');
			} else if (this.property.type) {
				return module.queryPropertyFilter(this.stepComponent, module.QueryTermFilter, 'generateJson');
			} else {
				return { 'eq': this.getVal() };
			}
		},

		countFilters: function () {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {
				return module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, 'countFilters');
			} else {
				return 1;
			}
		},

		indentNestedQueryBuilders: function () {
			if (this.property.getPropertyFilter() === module.QueryBuilderFilter) {
				module.queryPropertyFilter(this.stepComponent, module.QueryBuilderFilter, 'indentNestedQueryBuilders');
			}
		}
	};

	module.queryCascadeStep = function (elements, option) {
		var args = Array.prototype.slice.call(arguments).splice(1);
		return appseeUtils.createClassInstance(elements, 'as-query-cascade-step', QueryCascadeStep, option, args);
	};

	return qb;

}($.QueryBuilder || {}));
