import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './appsee-query-builder.scss';
import $ from 'jquery';

import appsee from './appsee-query-builder-plugin/appsee.js';
import './appsee-query-builder-plugin/appsee-search-coordinator';
import './appsee-query-builder-plugin/main';
import '@servicenow/now-icon';
import { userFilterType } from './filter-properties';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DOM_READY
} = actionTypes;

let componentInitialHTML = `<div id="users-query-builder"/>
							<div class="filters">
								<div class="filters__left-section">
									<div id="users-search-coordinator" class="sn-btn-container"/>
								</div>
							</div>`;

const view = () => {
	const render = () => {
		return (
			<div>
				<div className="users-container">
					{/*Query Builder*/ }

					<div id="users-query-builder"/>

					<div className="filters">
						<div className="filters__left-section">
							{/*Search Coordinator*/ }
							<div id="users-search-coordinator" className="sn-btn-container"/>
						</div>
					</div>
				</div>
			</div>
		);
	};
	return render();
};

const init = (state, host) => {
	setTimeout(() => {
		let { setQuery, initialState } = state.properties;
		let { filterProperties, filterType, apiKey} = initialState;

		if (apiKey) {
			appsee.context.apiKey = apiKey;
		}

		const queryBuilderParser = new $.QueryBuilder.module.QueryBuilderParser({});

		function renderQueryBuilder () {

			const getSessionExistsProperty = () => {
				let { sessionExistsProperty } = initialState;

				if (filterType !== userFilterType) {
					return [];
				}

				if (appsee.isReplayEnabled) {
					sessionExistsProperty.properties.push({ name: 'SessionHasVideo', description: 'Video Recorded', showOnlyInContext: {'platform': [0, 1, 'all']}, type: 'existenceBasedBoolean' });
				}

				return sessionExistsProperty;
			};

			function getSelectedPlatformProperties(properties) {
				return properties.filter((property) => {
					if (property.showOnlyInContext && property.showOnlyInContext.platform) {
						var showOnlyInContextPlatformValues = Object.values(property.showOnlyInContext.platform);
						return appsee.context.platform === 'web' ? showOnlyInContextPlatformValues.includes(2) : appsee.context.platform === 'chat' ? showOnlyInContextPlatformValues.includes(3) : !showOnlyInContextPlatformValues.includes(2);
					}
					return true;
				});
			}

			if (filterType === userFilterType && !filterProperties.some(filterProp => filterProp.name === 'session')) {
				var sessionExistsProperty = getSessionExistsProperty();

				// Filter session exists properties
				sessionExistsProperty.properties = getSelectedPlatformProperties(sessionExistsProperty.properties);
				filterProperties.push(sessionExistsProperty);
			}

			// Filter all properties
			filterProperties = getSelectedPlatformProperties(filterProperties);

			// Initialize the query builder
			let qb = host.shadowRoot.querySelector('#users-query-builder');
			let uc = host.shadowRoot.querySelector('.users-container');

			const $queryBuilder = $(qb).appseeQueryBuilder({
				autoPlatformFilter: false,
				floatsContainer: uc,
				properties: filterProperties,
				// Fixed by Lena
				context: {platform: 3},
				isCustomPropertiesSupported: true
			});

			// Register QB changed callback
			$queryBuilder.appseeQueryBuilder('registerStateChangedCallback', function (query) {

				// Get the query state from the QB
				var queryState = queryBuilderParser.jsonQueryStringToState(query,
					$queryBuilder.appseeQueryBuilder('getProperties'));

				// If there are no clauses in the query state or the first clause property is not platform,
				// we should delete the platform property from the QB context
				if (!queryState || !queryState.clauses || queryState.clauses.length === 0 ||
					queryState.clauses[0].property !== 'Platforms') {

					// Delete the platform property from the QB context
					// alert('Delete the platform property from the QB context');
					// Fixed by Lena, tbd - check if needed
					// $queryBuilder.appseeQueryBuilder('updateContext', 'platform', null);
					// console.log('is platform should be null ?');
				} else {
					// There is at least one clause in the query state and the first clause is platform,
					// let's get its value
					var value = parseInt(queryState.clauses[0].propertyFilterState.value);

					// If we can't parse the value to int (for example, in case of "Both Platforms"),
					// we set null
					value = isNaN(value) ? null : value;

					// Update the platform property in the QB context
					console.log('Update the platform property in the QB context to ' + value);
					$queryBuilder.appseeQueryBuilder('updateContext', 'platform', value);
				}
			});

			return $queryBuilder;
		}

		function invalidQueryCallback(error) {
			console.log('Appsee Error: Invalid Query');
		}

		let sc = host.shadowRoot.querySelector('#users-query-builder');
		let $searchCoordinator = $(sc).appseeSearchCoordinator({
			appseeQueryBuilderElement: renderQueryBuilder(),
			appseeQueryBuilderParser: queryBuilderParser,
			initialState,
			setQuery,
			invalidQueryCallback
		});
	}, 100);
};

createCustomElement('sn-appsee-query-builder-plugin', {
	renderer: {type: snabbdom},
	view,
	properties: {
		setQuery: { default: null },
		initialState: {
			default: {
				query: '{"and": []}',
				filterProperties: [],
				sessionExistsProperty: {},
				apiKey: ''
			}
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state, host }) => {
			init(state, host);
		},
		[COMPONENT_PROPERTY_CHANGED]: ({state, host, action: {payload: {name, previousValue, value}}}) => {
			if (name === 'initialState' && (previousValue.query !== value.query || (previousValue.apiKey !== value.apiKey && value.apiKey))) {
				let uc = host.shadowRoot.querySelector('.users-container');
				$(uc).html(componentInitialHTML);
				init(state, host);
			}
		}
	},
	styles: styles
});



