import moment from 'moment';
export default {

	getQueryGenWrapper: function (queryGen) {
		return function (propertyName, value) {
			var q = {};
			var queryGenResult = queryGen(value);

			// Build the clause only in case of non-empty result from the query generator method
			if (queryGenResult && Object.keys(queryGenResult).length > 0) {
				q[propertyName] = queryGenResult;
			}

			return q;
		};
	},

	getDurationInMillis: function (duration) {
		return duration * 1000;
	},

	getNumber: function (val, isFloatSupported) {

		// Set the parsing method
		var parsingMethod = isFloatSupported ? parseFloat : parseInt;

		if (val) {

			// Check if float is not supported and the value is float
			if (!isFloatSupported && val.indexOf('.') > -1) {
				return NaN;
			}

			return parsingMethod(Number(val));
		} else {
			return parsingMethod(val);
		}
	},

	getDSLFormattedDate: function (date) {
		return moment(date).format('YYYY-MM-DDTHH:mm:ss');
	},

	getDatePickerFormattedDate: function (date) {
		return moment(date).format('MM/DD/YYYY');
	},

	getFormattedDate: function (date) {
		return moment(date).format('YYYY-MM-DDTHH:mm:ss');
	},

	parseScreenFilterValue: function (value) {

		// The context data is identical for all the selected screens,
		// so we take this data from the first one
		var firstScreenData = JSON.parse(value[0]);
		var result = {
			'appId': firstScreenData['appId'],
			'platform': firstScreenData['platform'],
			'screenName': []
		};

		// Add all the selected screen names to the result data
		for (var i = 0; i < value.length; i++) {
			result['screenName'].push(JSON.parse(value[i])['screenName']);
		}

		return JSON.stringify(result);
	},

	setTopicSelectedValue: function (filterProperties, topicName) {
		for (var propertyIndex in filterProperties) {
			if (filterProperties[propertyIndex].name === 'Topics') {
				for (var cascadePropertyIndex in filterProperties[propertyIndex].cascade) {
					if (filterProperties[propertyIndex].cascade[cascadePropertyIndex].name === 'Topic') {
						filterProperties[propertyIndex].cascade[cascadePropertyIndex].selectedValue = topicName;
						return;
					}
				}
			}
		}
	},

	getTopicSelectedValue: function (filterProperties) {
		for (var propertyIndex in filterProperties) {
			if (filterProperties[propertyIndex].name === 'Topics') {
				for (var cascadePropertyIndex in filterProperties[propertyIndex].cascade) {
					if (filterProperties[propertyIndex].cascade[cascadePropertyIndex].name === 'Topic') {
						return filterProperties[propertyIndex].cascade[cascadePropertyIndex].selectedValue;
					}
				}
			}
		}
	}
};
