import './query-clause';
import appseeUtils from './appseeUtils';
import $ from 'jquery';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryClauseContainer = function (element, options) {
		this._init(element, options);
	};

	QueryClauseContainer.prototype = {

		_defaultOptions: {
			addNewClause: $.noop,
			clauseRemoved: $.noop,
			conjunctionChanged: $.noop,
			state: null,
			clauseOptions: {}
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend(true, {}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);

			this._filtersCountListener = this._filtersCountListener.bind(this);
			this._propertyWasSelected = this._propertyWasSelected.bind(this);

			this.options.clauseOptions.propertyWasSelected = this._propertyWasSelected;

			this.clauseWasRendered = false;
			this.guid = appseeUtils.guid();

			this.structure = {
				actions: $('<div class="as-clause-container__left"/>'),
				clause: $('<div class="as-clause-container__right"/>')
			};

			this.state = {
				showAdd: false,
				type: null,
				text: null,
				maxFiltersReached: false,
				propertyIsSelected: false
			};

			this.$element.empty().addClass('as-clause-container').attr('data-guid', this.guid)
				.append(this.structure.actions).append(this.structure.clause);

			this.setState(this.options.state);

			var parentGuid = null;
			var $parentContainer = this.$element.parent().closest('.as-clause-container');
			if ($parentContainer.length > 0) {
				parentGuid = $parentContainer.attr('data-guid');
			}

			this._scrollToSelf();

			module.appseeQueryBuilder(module.globals.rootElement, 'registerFiltersCountListener', this.guid, parentGuid, this._filtersCountListener);
		},

		_scrollToSelf: function () {
			module.globals.rootElement[0].scrollTop = this.$element.offset().top;
		},

		_filtersCountListener: function (maxFiltersReached) {
			this.setState({
				maxFiltersReached: maxFiltersReached
			});
		},

		_propertyWasSelected: function () {
			this.setState({
				propertyIsSelected: true
			});

			module.appseeQueryBuilder(module.globals.rootElement, 'filtersCountChanged');
		},

		_render: function () {
			if (!this.clauseWasRendered) {
				this.clauseWasRendered = true;
				var $clause = $('<div/>');
				this.structure.clause.append($clause);

				// Allowing the clause to decide by itself when the clause container should be deleted
				var self = this;
				this.options.clauseOptions['removeClauseCallback'] = function () {
					self.structure.actions.find('.as-clause-container__actions-remove').click();
				};
				module.queryClause($clause, this.options.clauseOptions);
			}

			this.structure.actions.empty().append(this._generateClauseActions());
		},

		_generateClauseActions: function () {
			var self = this;
			var $actions = $('<div class="as-clause-container__actions"/>');

			// Add "+" button
			var $addButton = $('<div title="Add Filter" class="as-clause-container__actions-add" disabled />').append('<now-icon icon="plus-fill" size="sm" />');

			if (this.state.showAdd) {
				if (this.state.maxFiltersReached) {
					$addButton.attr('title', 'You have reached the maximum filters that are enabled on your account.');
				} else if (!this.state.propertyIsSelected) {
					$addButton.attr('title', 'You need to select a property before adding a new one.');
				} else {
					$addButton.attr('disabled', false);
					$addButton.click(function () {
						self.options.addNewClause();
					});
				}
			} else {
				$addButton.css('visibility', 'hidden');
			}

			$actions.append($addButton);

			// Add "X" button
			$actions.append($('<div title="Remove Filter" class="as-clause-container__actions-remove"/>').append('<now-icon icon="close-fill" size="md" />')
				.click(function () {
					self.$element.remove();
					self.options.clauseRemoved();
					module.appseeQueryBuilder(module.globals.rootElement, 'unregisterFiltersCountListener', self.guid);
				})
			);

			var $conjunction = $('<div class="as-clause-container__actions-conjunction"/>')
				.addClass('as-clause-container__actions-conjunction--' + this.state.type);
			switch (this.state.type) {
				case 'root':
					$conjunction.append('<span>' + this.state.text + '</span>');
					break;
				case 'conjunctionMenu':
				// init dropdown and select the text
					var $select = $('<select><option value="and">' + t('And') + '</option><option value="or">' + t('Or') + '</option></select>');
					$select.children('option[value="' + this.state.text + '"]').prop('selected', true);
					$select.change(function () {
						self.options.conjunctionChanged($(this).val());
					});
					$conjunction.append($select);
					$select.appseeSelect({
						container: module.globals.floatsContainer,
						classExtension: 'modified-bootstrap-select',
						replaceCaret: true
					});
					break;
				case 'conjunctionText':
				// init "dropdown" select disabled
					$conjunction.append('<span>' + this.state.text + '</span>');
					break;
			}
			$actions.append($conjunction);

			return $actions;
		},

		/******************
         * Public methods *
         ******************/

		setState: function (state) {
			var oldState = this.state;
			this.state = $.extend({}, this.state, state);

			// Render only if the state is different
			if (JSON.stringify(oldState) !== JSON.stringify(this.state)) {
				this._render();
			}
		},

		setClauseState: function (clauseState, callback) {
			module.queryClause(this.structure.clause.children('.as-query-clause'), 'setState', clauseState, callback);
		},

		validate: function () {
			return module.queryClause(this.structure.clause.children('.as-query-clause'), 'validate');
		},

		generateJson: function () {
			return module.queryClause(this.structure.clause.children('.as-query-clause'), 'generateJson');
		},

		countFilters: function () {
			return module.queryClause(this.structure.clause.children('.as-query-clause'), 'countFilters');
		},

		indentNestedQueryBuilders: function () {
			module.queryClause(this.structure.clause.children('.as-query-clause'), 'indentNestedQueryBuilders');
		}
	};

	module.queryClauseContainer = function (elements, option) {
		var args = Array.prototype.slice.call(arguments).splice(1);
		return appseeUtils.createClassInstance(elements, 'as-clause-container', QueryClauseContainer, option, args);
	};

	return qb;

}($.QueryBuilder || {}));
