import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import '@servicenow/now-icon';
import '@servicenow/now-chart-sparkline';
import { t } from 'sn-translate';

import styles from './sn-va-score-trend.scss';

const POSITIVE_COLOR_VALUE = '#25832b';
const NEGATIVE_COLOR_VALUE = '#ea3c10';

const CHART_WIDTH_IN_PIXELS = 75;
const CHART_HEIGHT_IN_PIXELS = 25;

const view = (state, {dispatch}) => {
	const {
		score,
		change,
		chartData,
		trendId,
		textLabel,
		textLabelFontSize,
		fallbackText,
	} = state.properties;

	const {
		chartConfig
	} = state;

	const isChangePositive = change && !change.includes('-');

	// Dispatch an action when user clicks on the small chart
	// Payload will include chart data
	const chartClickHandler = () => {
		dispatch('SN_VA_SCORE_TREND#CHART_CLICKED', { chartData, trendId });
	};

	const getScoreElement = () => (
		<div className="score-trend__value-container">
			<span aria-label={`score value: ${score}`} tabindex="-1" className="score-trend__value">{score}</span>
		</div>
	);

	// Support dispatching CHART_CLICKED with enter key
	const onKeyupHandler = e => {
		if (e.which === 13) {
			dispatch('SN_VA_SCORE_TREND#CHART_CLICKED', { chartData, trendId });
		}
	};

	const getChartAreaElement = () => {
		const updatedChartConfig = {
			...chartConfig,
			seriesConfig: {
				series: {
					x: 'timestamp',
					y: 'y0',
					type: 'area',
					color: isChangePositive ? POSITIVE_COLOR_VALUE : NEGATIVE_COLOR_VALUE
				}

			}
		};

		return (
			<div tabindex="0" aria-label="Trend chart" className="score-trend__chart-container" on-keyup={onKeyupHandler} on-click={chartClickHandler}>
				<div style={{
					width: `${CHART_WIDTH_IN_PIXELS}px`,
					height: `${CHART_HEIGHT_IN_PIXELS}px`
				}}>
					<now-chart-sparkline
						tabindex="-1"
						aria-label="chart indicating the trend"
						{...updatedChartConfig}
						seriesData={chartData} />
				</div>
			</div>
		);
	};

	const getChangeTextElement= () => {
		const changeClass = isChangePositive ? 'score-trend__change-up' : 'score-trend__change-down';
		const changeText = t(isChangePositive ? 'increase' : 'decrease');
		const changeIndicator = isChangePositive ? '+' : '';

		const changeString = `${changeIndicator}${change}`;

		const changeLabelStyles = textLabelFontSize ? {
			'font-size': textLabelFontSize
		} : null;

		return (
			<div tabindex="-1" aria-label={`A ${changeText} of ${changeString}`} className="score-trend__change-row">
				<now-icon
					icon={isChangePositive ? 'caret-up-fill' : 'caret-down-fill'}
					size="lg"
					className={`score-trend__change-icon score-trend__change-icon--${isChangePositive ? 'up' : 'down'}`}
				/>
				<div className={`score-trend__change-value ${changeClass}`}>{changeString}</div>
				<div style={changeLabelStyles} className="score-trend__change-label ">{`${textLabel}`}</div>
			</div>
		);
	};

	const getFallbackTextElement= () => {
		const changeLabelStyles = textLabelFontSize ? {
			'font-size': textLabelFontSize
		} : null;

		return (
			<div tabindex="-1" aria-label={`${fallbackText}`} className="score-trend__change-row">
				<div style={changeLabelStyles} className="score-trend__change-label ">{`${fallbackText}`}</div>
			</div>
		);
	};

	const chartDataExists = () => chartData && Array.isArray(chartData) && chartData.length > 0;

	return (
		<div className="score-trend">
			<div className="score-trend__value-row">
				{ score ? getScoreElement() : null }

				{ chartDataExists() ? getChartAreaElement() : null }
			</div>

			{ change ? getChangeTextElement() : getFallbackTextElement() }
		</div>
	);
};

// This component allows you to show:
// 	- A score value
// - Percentage of change
// - A sparkline chart displaying the trend over time
// - A Model displaying a more detailed time series chart
//
// <sn-va-score-trend
//    trendId="usersScore"
//    score="300K"
//    change="12%"
//	  textLabel="From 10/08/20 to 11/04/20"
//    textLabelFontSize="12px"
//    chartData={[
// 	   {timestamp: 1501843274096, y0: 282.67},
// 	   {timestamp: 1511434701664, y0: 300.00},
// 	   {timestamp: 1521026129232, y0: 307.63}
//    ]}
// >
// </sn-va-score-trend>

createCustomElement('sn-va-score-trend', {
	renderer: {type: snabbdom},
	view,
	properties: {
		// Used to identify a single score trend card
		// Will be included in the "SN_VA_SCORE_TREND#CHART_CLICKED" action payload
		trendId: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		// Score to display (the biggest font size).
		// type: `string`
		// default value: ''
		// Required
		score: {
			required: true,
			schema: {
				type: 'string'
			},
			default: ''
		},
		// Score change value.
		// type: `string`
		// default value: ''
		// Required
		change: {
			required: true,
			schema: {
				type: 'string'
			},
			default: ''
		},
		// Text label to display near change value
		textLabel: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		// Text label font size
		textLabelFontSize: {
			schema: {
				type: 'string'
			},
			default: null
		},
		// Chart data values - displays as a mini sparkline chart near the score value.
		// When clicking on the sparkline mini chart a modal with a more detailed time series chart would come up.
		// type: `array`
		// default value: null
		// x property should be named 'timestamp', y property should be named 'y0'
		// Not Required
		chartData: {
			schema: {
				type: 'array'
			},
			default: []
		},
		// Fallback text when change props is empty
		fallbackText: {
			schema: {
				type: 'string'
			},
			default: ''
		},
	},
	dispatches: {
		// Dispatches when user clicks on the small chart
		// Payload will include chart data and trend ID
		// @scheme: { chartData: array, trendId: string }
		'SN_VA_SCORE_TREND#CHART_CLICKED': {}
	},
	initialState: {
		chartConfig: {
			width: CHART_WIDTH_IN_PIXELS,
			height: CHART_HEIGHT_IN_PIXELS
		}
	},
	styles
});
