import appseeUtils from './appseeUtils';
import valueHelper from './value-helper';
import $ from 'jquery';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	module.valueComponents = {

		// Bound values
		boundSelect: {
			generator: function (params) {

				// Set the basic selector params
				var selectorParams = {
					values: params.property.values || params.property.fetchValuesUrl,
					description: t('Select') + ' ' + (params.property.singularDescription || params.property.description),
					noValuesText: '('+ t('No') +' ' + params.property.description + ')'
				};

				// Add 'grouped items' param (if needed)
				if (params.property.typeParams && params.property.typeParams.groupedItems) {
					selectorParams['groupedItems'] = true;
				}

				// Add mutiple customization for with no custom condition customization property
				let mutiple = true;
				if (params.property.conditionsCustomizations && params.property.conditionsCustomizations.componentParams && params.property.conditionsCustomizations.componentParams.multiple !== undefined) {
					mutiple = params.property.conditionsCustomizations.componentParams.multiple;
				}

				// Add 'multiple' params (if needed)
				if (params.condition.componentParams && params.condition.componentParams.multiple && mutiple) {
					selectorParams['multiple'] = true;
					selectorParams['description'] = t('Select') + ' ' + params.property.pluralDescription;
					selectorParams['noneSelectedText'] = t('Select') + ' ' + params.property.pluralDescription;
				}

				return module.selector($('<div class="as-query-value-component" />'), selectorParams);
			},
			valuesLoader: function (component, customFetchValuesUrl, valuesLoadedCallback, valuesTransformer) {
				module.selector(component, 'loadValues', customFetchValuesUrl, valuesLoadedCallback, valuesTransformer);
			},
			valueGetter: function (component) {
				return module.selector(component, 'getVal');
			},
			valueSetter: function (component, value) {
				module.selector(component, 'setVal', value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('select.as-select').on('changed.bs.select hide.appsee.select', validationMethod);
			},
			registerChangeCallback: function (component, changeCallback) {
				component.find('select.as-select').on('changed.bs.select', function (e) {
					changeCallback(module.selector(component, 'getVal'));
				});
			}
		},
		boundActions: {
			generator: function () {
				return module.actionSelector($('<div class="as-query-value-component" />'));
			},
			valuesLoader: function (component, customFetchValuesUrl, valuesLoadedCallback) {
				module.actionSelector(component, 'fetch', customFetchValuesUrl, valuesLoadedCallback);
			},
			valueGetter: function (component) {
				return module.actionSelector(component, 'getVal');
			},
			valueSetter: function (component, value) {
				module.actionSelector(component, 'setVal', value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.on('hide.appsee.actionSelector', validationMethod);
			}
		},

		// Unbound List
		unboundListInput: {
			generator: function () {
				return $('<ul class="as-query-value-component as-query-value-unbound-list-input" />').appseeMultiTextInput({
					itemDesc: 'Value',
					readOnly: false,
					allowSpaces: true,
					removeConfirmation: true,
					placeholderText: t('Type values') + '...',
					itemLimit: 20,
					width: '350px'
				});
			},
			valueGetter: function (component) {
				return component.tagit('assignedTags');
			},
			valueSetter: function (component, value) {

				// Clean the current items
				component.tagit('removeAll');

				// Set all the values in the given array
				for (var i = 0; i < value.length; i++) {
					component.tagit('createTag', value[i]);
				}
			},
			registerValidationMethod: function (component, validationMethod) {
				component.focusout(validationMethod);
			}
		},

		// String
		stringInput: {
			generator: function (params) {
				return $('<input type="text" class="as-query-value-component as-query-value-string-input m-wrap" />')
					.attr('placeholder', params.property.isTypeInferred ? t('Enter value') : params.property.description);
			},
			valueGetter: function (component) {
				return component.val();
			},
			valueSetter: function (component, value) {
				component.val(value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.blur(validationMethod);
				component.focusout(validationMethod);
			}
		},

		// Number
		numberInput: {
			generator: function (params) {

				// Float support is false by default, can be overidden by the property definition
				var isFloatSupported = (params.property.typeParams && params.property.typeParams.isFloatSupported) || false;

				// Wider input is false by default, can be overridden by the property definition
				var setWiderInput = (params.property.typeParams && params.property.typeParams.setWiderInput) || false;
				return $('<div class="as-query-value-component" data-is-float-supported="' + isFloatSupported + '" />')
					.append('<input type="number" class="as-query-value-number-input no-spinner m-wrap ' +
                    (setWiderInput ? ' as-query-value-number-input--wider' : '') + '" />');
			},
			valueGetter: function (component) {
				return valueHelper.getNumber(component.find('input').val(),
					appseeUtils.parseBool(component.attr('data-is-float-supported')));
			},
			valueSetter: function (component, value) {
				component.find('input').val(value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('input[type="number"]').blur(validationMethod);
				component.focusout(validationMethod);
			}
		},
		numberRange: {
			generator: function (params) {

				// Float support is false by default, can be overidden by the property definition
				var isFloatSupported = (params.property.typeParams && params.property.typeParams.isFloatSupported) || false;

				// Wider input is false by default, can be overridden by the property definition
				var setWiderInput = (params.property.typeParams && params.property.typeParams.setWiderInput) || false;
				return $('<div class="as-query-value-component" data-is-float-supported="' + isFloatSupported + '" />')
					.append('<input type="number" min="0" data-order="1" class="as-query-value-number-input no-spinner m-wrap' +
                    (setWiderInput ? ' as-query-value-number-input--wider' : '') + '" />')
					.append('<span class="as-help-text as-help-text--space-1x">' + t('and') + '</span>')
					.append('<input type="number" min="0" data-order="2" class="as-query-value-number-input no-spinner m-wrap' +
                    (setWiderInput ? ' as-query-value-number-input--wider' : '') + '" />');
			},
			valueGetter: function (component) {
				var isFloatSupported = appseeUtils.parseBool(component.attr('data-is-float-supported'));
				var start = valueHelper.getNumber(component.find('input[data-order="1"]').val(), isFloatSupported);
				var end = valueHelper.getNumber(component.find('input[data-order="2"]').val(), isFloatSupported);

				// Swap if start is greater than end
				if (start > end) {
					start = end + (end = start, 0);
				}

				return { start: start, end: end };
			},
			valueSetter: function (component, value) {
				component.find('input[data-order="1"]').val(value['start']);
				component.find('input[data-order="2"]').val(value['end']);
			},
			registerValidationMethod: function (component, validationMethod) {

				// In case of focus-out from the first input, we should check if the second input got the new
				// focus and make validation accordingly
				component.find('input[data-order="1"]').focusout(function (e) {

					// Check if the second input got the focus (means, if the user clicked on the second input)
					var isSecondInputGotFocused =
                        e.relatedTarget !== null && e.relatedTarget.getAttribute('data-order') === '2';

					if (isSecondInputGotFocused) {
						// If the user clicked on the second input, we make validation only for the first input
						var isFloatSupported = appseeUtils.parseBool(component.attr('data-is-float-supported'));
						var value = valueHelper.getNumber($(this).val(), isFloatSupported);
						module.components.validateValueComponent(value, 'number', component);
					} else {
						// If the user clicked outside of both inputs, we make validation for all the value component
						validationMethod();
					}
				});

				// In case of focus-out from the second input, we make the regular validation for all the
				// value component
				component.find('input[data-order="2"]').focusout(validationMethod);
			}
		},

		// Boolean
		booleanInput: {
			generator: function () {
				return module.selector($('<div class="as-query-value-component as-query-value-boolean-input" />'),
					{ values: { true: t('Yes'), false: t('No') }, description: t('Select Yes/No') });
			},
			valuesLoader: function (component, customFetchValuesUrl, valuesLoadedCallback) {
				module.selector(component, 'loadValues', customFetchValuesUrl, valuesLoadedCallback);
			},
			valueGetter: function (component) {
				return appseeUtils.parseBool(module.selector(component, 'getVal'));
			},
			valueSetter: function (component, value) {
				module.selector(component, 'setVal', value.toString());
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('select.as-select').on('changed.bs.select hide.appsee.select', validationMethod);
			},
			defaultValue: true
		},

		// Duration
		durationInput: {
			generator: function () {
				return $('<div class="as-query-value-component" />')
					.append('<input type="number" min="0" class="as-query-value-duration-input m-wrap" />')
					.append('<span class="as-help-text as-help-text--space-1x">' + t('seconds') + '</span>');
			},
			valueGetter: function (component) {
				return valueHelper.getNumber(component.find('input').val(), false);
			},
			valueSetter: function (component, value) {
				component.find('input').val(value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('input[type="number"]').blur(validationMethod);
				component.focusout(validationMethod);
			},
			defaultValue: 0
		},
		durationRange: {
			generator: function () {
				return $('<div/>').addClass('as-query-value-component')
					.append('<input type="number" min="0" data-order="1" class="as-query-value-duration-input m-wrap" />')
					.append('<span class="as-help-text as-help-text--space-1x">' + t('and') + '</span>')
					.append('<input type="number" min="0" data-order="2" class="as-query-value-duration-input m-wrap" />')
					.append('<span class="as-help-text as-help-text--space-1x">' + t('seconds') + '</span>');
			},
			valueGetter: function (component) {
				var start = valueHelper.getNumber(component.find('input[data-order="1"]').val());
				var end = valueHelper.getNumber(component.find('input[data-order="2"]').val());

				// Swap if start is greater than end
				if (start > end) {
					start = end + (end = start, 0);
				}

				return { start: start, end: end };
			},
			valueSetter: function (component, value) {
				component.find('input[data-order="1"]').val(value['start']);
				component.find('input[data-order="2"]').val(value['end']);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('input[type="number"]').blur(validationMethod);
				component.focusout(validationMethod);
			},
			defaultValue: {
				start: 0,
				end: 0
			}
		},

		// Date
		datePicker: {
			generator: function (params) {
				return module.internalUtils.createDatePicker(false, params.parentDiv);
			},
			valueGetter: function (component) {
				return component.val();
			},
			valueSetter: function (component, value) {
				component.data('daterangepicker').setStartDate(value);
				component.data('daterangepicker').setEndDate(value);
				component.val(value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.on('hide.daterangepicker', validationMethod);
			}
		},
		dateRange: {
			generator: function (params) {
				return module.internalUtils.createDatePicker(true, params.parentDiv);
			},
			valueGetter: function (component) {

				var value = component.val();

				// If range is selected
				if (value) {
					value = { startDate: value.split('-')[0].trim(), endDate: value.split('-')[1].trim() };
				}

				return value;
			},
			valueSetter: function (component, value) {
				component.data('daterangepicker').setStartDate(value['startDate']);
				component.data('daterangepicker').setEndDate(value['endDate']);
				component.val(value['startDate'] + ' - ' + value['endDate']);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.on('hide.daterangepicker', validationMethod);
			}
		},
		dateRelative: {
			generator: function (params) {
				var timeUnits = {};

				// Add time options to the time units (if required)
				if (params.property.typeParams && params.property.typeParams.addTimeOptions) {
					timeUnits = { 'minutes': t('minutes'), 'hours': t('hours') };
				}

				// Add the default time units
				timeUnits = { ...timeUnits, 'days': t('days'), 'weeks': t('weeks'), 'months': t('months') };
				return $('<div class="as-query-value-component as-query-value-date-relative-input" />')
					.append('<input type="number" min="1" class="as-query-value-number-input as-query-value-date-relative-input___value m-wrap" />')
					.append(module.selector($('<div class="as-query-value-date-relative-input__time-unit" />'), { values: timeUnits, description: t('Select Time Unit') }));
			},
			valuesLoader: function (component, customFetchValuesUrl, valuesLoadedCallback) {
				module.selector(component.children('div'), 'loadValues', null, valuesLoadedCallback);
			},
			valueGetter: function (component) {
				return {
					value: valueHelper.getNumber(component.children('input').val(), false),
					timeUnit: module.selector(component.children('div'), 'getVal')
				};
			},
			valueSetter: function (component, value) {
				component.children('input').val(value.value);
				module.selector(component.children('div'), 'setVal', value.timeUnit);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('input[type="number"]').blur(validationMethod);
				component.focusout(validationMethod);
			},
			defaultValue: {
				value: 30,
				timeUnit: 'days'
			}
		},

		hiddenValue: {
			generator: function (ignored) {
				return $('<div class="as-query-value-component" />').append('<input type="hidden" />');
			},
			valueGetter: function (component) {
				const value = component.find('input').val();
				return !value ? value : JSON.stringify(value);
			},
			valueSetter: function (component, value) {
				component.find('input').val(value);
			},
			registerValidationMethod: function (component, validationMethod) {
			}
		},

		// Platform
		platformInput: {
			generator: function (params) {
				var values = appseeUtils.cloneObject(module.internalUtils.platformsEnum);

				// Drop the Non-mobile platforms unless requested to
				if (!((params.condition.componentParams && params.condition.componentParams.showAllPlatformsOption) ||
					(params.property.typeParams && params.property.typeParams.showAllPlatformsOption))) {
					delete values['2'];
					delete values['3'];
				}

				// Add "Both Platforms" option (if needed)
				if ((params.condition.componentParams && params.condition.componentParams.addAllOption) ||
                    (params.property.typeParams && params.property.typeParams.addAllOption)) {
					values['[0, 1]'] = 'Both Platforms';
				}

				// Add "Either Platform" option (if needed)
				if ((params.condition.componentParams && params.condition.componentParams.addEitherPlatformOption) ||
                    (params.property.typeParams && params.property.typeParams.addEitherPlatformOption)) {
					values['either'] = 'Either Platform';
				}

				return module.selector($('<div class="as-query-value-component as-query-value-platform-input" />'),
					{ values: values, description: 'Select Platform' });
			},
			valuesLoader: function (component, customFetchValuesUrl, valuesLoadedCallback) {
				module.selector(component, 'loadValues', customFetchValuesUrl, valuesLoadedCallback);
			},
			valueGetter: function (component) {
				return module.selector(component, 'getVal');
			},
			valueSetter: function (component, value) {
				module.selector(component, 'setVal', value);
			},
			registerValidationMethod: function (component, validationMethod) {
				component.find('select.as-select').on('changed.bs.select hide.appsee.select', validationMethod);
			},
			registerChangeCallback: function (component, changeCallback) {
				component.find('select.as-select').on('changed.bs.select', function (e) {
					changeCallback($(this).val());
				});
			},
			setDisabled: function (component, disabled) {
				module.selector(component, 'setDisabled', disabled);
			}
		}
	};

	return qb;

}($.QueryBuilder || {}));
