import {createCustomElement} from '@servicenow/ui-core';
import '@servicenow/now-uxf-visualization-connected';
import '@servicenow/now-button';
import '@servicenow/now-text-link';
import '@servicenow/now-dropdown';
import '../../sn-va-slotted-tile';
import styles from './styles.scss';
import {
	SN_CUSTOM_EVENT_CHART_PROPERTY_VIEW_CHANGED,
	SN_CUSTOM_EVENT_CHART_DATE_BUCKET_CHANGED,
	SN_CUSTOM_EVENT_CHART_DOWNLOAD_CLICKED,
	SN_CUSTOM_EVENT_CHART_VISUALIZATION_CLICKED,
	DOWNLOAD,
	DAILY,
	WEEKLY,
	MONTHLY
} from '../constants';

const DATA_VIEWS = ['chart-donut', 'list', 'chart-line'];

const view = (state, {updateState}) => {
	const {
		activeView,
		properties: {
			id,
			title,
			showTags,
			tags,
			showDownloadIcon,
			downloadLinkText,
			downloadLinkHref,
			showDateBucketSelector,
			dateBuckets,
			selectedDateBucket,
			parConfiguration,
			listData
		}
	} = state;
	return (
		<div className="sn-custom-event-chart-wrapper">
			<sn-va-slotted-tile label={title} id={id}>
				<div slot="header">
					<div className="chart-icons">{
						DATA_VIEWS.map(chartView =>
							<now-button-iconic
								bare="true"
								variant="tertiary"
								size="md"
								icon={`${chartView}-${chartView === activeView ? 'fill' : 'outline'}`}
								append-to-payload={{ event: `toggle-view-${chartView}` }}
								config-aria={{
									button: {'aria-label': `Display ${chartView} for ${title} custom event`}
								}}
							/>)}
					</div>
				</div>
				<div slot="content">
					<div className="tag-row">
						{
							showTags
								? (
									<span className="tag-container">
										{
											tags.map(tag => (
												<span className="occurrence-tag">
													{tag}
												</span>
											))
										}
									</span>
								) : null
						}
						{
							showDownloadIcon
								? (
									<span>
										<now-button-iconic
											bare={true}
											variant="tertiary"
											size="md"
											icon="download-fill"
											tabindex="-1"
										/>
										<now-text-link
											href={downloadLinkHref}
											label={downloadLinkText}
											download={true}
											variant="primary"
											append-to-payload={{ event: 'download-csv' }}
										/>
									</span>
								) : null
						}
					</div>
					{
						showDateBucketSelector
							? (
								<div className="view-bucket-row">
									<now-dropdown
										placeholder={dateBuckets && dateBuckets[0] ? dateBuckets[0].label : ''}
										items={dateBuckets}
										selectedItems={[selectedDateBucket]}
										showPadding={false}
										variant="secondary"
										size="sm"
										bare={true}
									/>
								</div>
							) : null
					}
					<div className="charts-area">
						{
							activeView !== 'list'
								? (
									<div className="par-chart">
										<now-uxf-visualization-connected
											{...parConfiguration}
										/>
									</div>
								) : null
						}
						{
							activeView === 'list'
								? (
									<div className="list-view">
										{
											listData.map(listItem => (
												<div className="list-item">
													<div className="name">
														{ listItem.name }
													</div>
													<div className="count">
														{ listItem.count }
													</div>
												</div>
											))
										}
									</div>
								) : null
						}
					</div>
				</div>
			</sn-va-slotted-tile>
		</div>
	);
};

export default createCustomElement('sn-custom-event-chart', {
	view,
	initialState: {
		activeView: DATA_VIEWS[0]
	},
	properties: {
		id: {
			default: ''
		},
		title: {
			default: ''
		},
		showTags: {
			default: true,
		},
		tags: {
			default: []
		},
		showDownloadIcon: {
			default: true
		},
		downloadLinkText: {
			default: DOWNLOAD
		},
		downloadLinkHref: {
			default: ''
		},
		showDateBucketSelector: {
			default: false
		},
		selectedDateBucket: {
			default: 'day'
		},
		dateBuckets: {
			default: [
				{ id: 'day', label: DAILY },
				{ id: 'week', label: WEEKLY },
				{ id: 'month', label: MONTHLY }
			]
		},
		parConfiguration: {
			default: {}
		},
		listData: {
			default: []
		}
	},
	actionHandlers: {
		// Either Chart Type Changed, or Download Clicked
		'NOW_BUTTON_ICONIC#CLICKED': ({ action, dispatch, properties, updateState }) => {
			const { event='' } = action.payload;
			if (event.match(/toggle-view-(.*)/)) {
				const newView = RegExp.$1;
				updateState({
					activeView: newView
				});
				dispatch(SN_CUSTOM_EVENT_CHART_PROPERTY_VIEW_CHANGED, {
					id: properties.id,
					selectedView: newView
				});
			}
		},
		'NOW_TEXT_LINK#CLICKED': ({ action, dispatch, properties, updateState }) => {
			dispatch(SN_CUSTOM_EVENT_CHART_DOWNLOAD_CLICKED, {
				id: properties.id
			});
		},
		// Date Bucket Changed
		'NOW_DROPDOWN#ITEM_CLICKED': ({ action, dispatch, properties }) => {
			dispatch(SN_CUSTOM_EVENT_CHART_DATE_BUCKET_CHANGED, {
				id: properties.id,
				selectedBucket: action.payload.item
			});
		},
		// PAR Visualization Clicked
		'ITEM_SELECTED': ({ action, dispatch, properties }) => {
			dispatch(SN_CUSTOM_EVENT_CHART_VISUALIZATION_CLICKED, {
				id: properties.id,
				parPayload: action.payload
			});
		}
	},
	styles
});
