import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';
import {t} from 'sn-translate';
import styles from './sn-va-slotted-tile.scss';

const tileStyle = {
	borderStyle: 'solid',
	minHeight: '100%'
};

const headerStyle = {
	display: 'flex',
	justifyContent: 'space-between',
	borderBottomStyle: 'solid'
};

const labelStyle = {
	overflow: 'hidden',
	textOverflow: 'ellipsis',
	whiteSpace: 'nowrap'
};

const view = (state, { updateState }) => {
	const { properties } = state;
	const { label, tooltipContent } = properties;
	let lineHeight = 0;
	const getLineHeight = (ref) => lineHeight = ref.getBoundingClientRect().height;
	const updateHeaderHeight = (ref) => setTimeout(() => {
		const height = Math.max.apply(null,
			ref.assignedNodes()
				.map((slotElement) => slotElement.getBoundingClientRect().height)
		);
		updateState({headerHeight: Math.max(lineHeight, height)});
	});
	const tooltipTextExists = tooltipContent && tooltipContent.length > 0;
	const ariaLabel = `${t(label)}${ tooltipTextExists ? `. ${t(tooltipContent)}` : ''}`;

	return (
		<div className="sn-va-slotted-tile" style={tileStyle}>
			<div className="sn-va-slotted-tile-header" style={{ ...headerStyle, lineHeight: state.headerHeight ? `${state.headerHeight}px` : 'auto' }}>
				<span hook-insert={({elm}) => getLineHeight(elm)} aria-label={ariaLabel} role="banner" tabindex="0" style={labelStyle}>{t(label)}</span>
				{tooltipTextExists &&
				<now-tooltip
					id="sn-va-slotted-tile-label-tooltip"
					hook-insert={({elm}) => elm.targetRef = elm.previousElementSibling}
					content={t(tooltipContent)}
					delay={300}
					position={['top-center bottom-center','bottom-center top-center','center-end center-start','center-start center-end','top-end top-start','bottom-end bottom-start','top-start top-end','bottom-start bottom-end']}
					offset={8}
					ariaLabel={t(tooltipContent)}
					ariaHidden="false"
					role="tooltip"
				>
				</now-tooltip>}
				<span>
					<slot hook-insert={({elm}) => updateHeaderHeight(elm)} name="header"></slot>
				</span>
			</div>
			<slot name="content"></slot>
		</div>
	);
};

createCustomElement('sn-va-slotted-tile', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		label: {
			schema: {
				type: 'string'
			},
			default: ''
		},
		tooltipContent: {
			schema: {
				type: 'string'
			},
			default: ''
		}
	},
	styles,
	slots: {
		content: {},
		header: {}
	},
	initialState: {
		headerHeight: 0
	}
});
