/// <reference path="internal-utils.js"/>
/// <reference path="components.js"/>

import './components';
import $ from 'jquery';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryTermFilter = function (element, options) {
		this._init(element, options);
	};

	QueryTermFilter.prototype = {

		_defaultOptions: {
			property: Object() // QueryProperty object
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function(options) {
			options = $.extend({}, this._defaultOptions, this.$element.data(), options);
			return options;
		},

		_init: function(element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);
			this.property = this.options.property;

			this.structure = {
				propertyType: $('<div/>').addClass('as-query-property-type').hide(),
				condition: $('<div/>').addClass('as-query-condition'),
				value: $('<div/>').addClass('as-query-value')
			};
		},

		_conditionSelectChanged: function(selectedCondition, valueComponentRenderedCallback) {

			// Call the change callback (if exists)
			if (this.options.changeCallback) {
				this.options.changeCallback(null);
			}

			this.currentCondition = selectedCondition;
			this._renderValueObject(null, valueComponentRenderedCallback);
		},

		_setSelectedCondition: function(condition, valueComponentRenderedCallback) {
			var $conditionSelect = this.structure.condition.find('select.as-query-condition-select.as-select');
			$conditionSelect.val(condition).appseeSelect('refresh');
			this._conditionSelectChanged(condition, valueComponentRenderedCallback);
		},

		_propertyTypeSelectChanged: function (selectedPropertyType, openConditionSelect) {
			this.currentPropertyType = selectedPropertyType;

			// Call the change callback (if exists)
			if (this.options.changeCallback) {
				this.options.changeCallback(null);
			}

			// Render the conditions select
			this._renderConditions();

			this.structure.propertyType.hide();
			if (openConditionSelect) {
				this.structure.condition.find('button').click();
			}
		},

		_setSelectedPropertyType: function(propertyType) {
			var $propertyTypeSelect = this.structure.propertyType.find('select.as-query-property-type-select.as-select');
			$propertyTypeSelect.val(propertyType).appseeSelect('refresh');
			this._propertyTypeSelectChanged(propertyType, false);
		},

		_renderPropertyTypes: function(fetchValuesUrl, renderFinishedCallback) {

			// Generate the property type items select
			var $propertyTypeSelect = module.internalUtils.generateDropdown('propertyType',
				module.components.getPropertyTypeItems(),
				{ selectClassName: 'as-query-property-type-select' });

			// Register property type change event
			var self = this;
			$propertyTypeSelect.on('change hide.appsee.select', function () {
				self._propertyTypeSelectChanged($(this).val(), true);
			});

			// Set the current property type selected
			$propertyTypeSelect.val(this.currentPropertyType);

			// Appending the select to the elements and only then initiate appseeSelect (won't work otherwise)
			this.structure.propertyType.empty().append($propertyTypeSelect);
			$propertyTypeSelect.appseeSelect({
				container: module.globals.floatsContainer,
				size: module.globals.dropdownSize,
				classExtension: 'modified-bootstrap-select',
				replaceCaret: true
			});

			// Render the conditions select
			this._renderConditions(fetchValuesUrl, renderFinishedCallback);
		},

		_renderConditions: function(fetchValuesUrl, renderFinishedCallback) {

			// Get the relevant condition items according to the property type
			var conditionItems = module.components.getConditionItems(this.property, this.currentPropertyType);

			// Generate the conditions select, select the first option by default
			var $conditionSelect =
                module.internalUtils.generateDropdown('condition',
                	conditionItems,
                	{ selectClassName: 'as-query-condition-select', defaultSelected: true });

			var options = {
				container: module.globals.floatsContainer,
				size: module.globals.dropdownSize,
				classExtension: this.property.isTypeInferred ? 'modified-bootstrap-select modified-bootstrap-select--type-select' : 'modified-bootstrap-select',
				replaceCaret: true
			};

			// If this property is type inferred (for example: custom user properties), add the option for changing the type
			if (this.property.isTypeInferred) {
				var content = '<span class=\'type-headline\'><span style=\'opacity: 0.65;\'>' +
                    module.components.getPropertyTypeText(this.currentPropertyType) +
                    '</span><span style=\'font-size:11px; color: #18bcf2; cursor: pointer;\'>change type</span></span>';
				$conditionSelect.prepend(
					'<option value="-1" style="border-bottom: 1px solid #ebebeb; background-color: #f7f7f7; cursor: default;" data-content="' +
                    content +
                    '"></option>');
			}

			// If only one condition exists, disable the multi select
			if ($conditionSelect.children('option').length === 1) {

				// No opacity for disabled button
				$conditionSelect.prop('disabled', true);

				// Remove the caret
				options.template = { caret: '<div/>' };

				// Since caret is removed, button needs to be smaller
				$conditionSelect.addClass('no-toggle');
			}

			// Appending the select to the elements and only then initiate appseeSelect (won't work otherwise)
			this.structure.condition.show().empty().append($conditionSelect);
			$conditionSelect.appseeSelect(options);

			// Update current condition
			this.currentCondition = $conditionSelect.val();

			// Register change events
			var self = this;
			$conditionSelect.on('changed.bs.select', function() {
				var val = $(this).val();

				// If value is -1, it's change property type
				if (val === '-1') {
					self.structure.value.hide();
					self.structure.condition.hide();
					self.structure.propertyType.show().find('button').click();
					return;
				}

				self._conditionSelectChanged(val);
			});

			// Hide the condition section (if needed)
			if (this.property.conditionsCustomizations && this.property.conditionsCustomizations.hideConditionsSelect) {
				this.structure.condition.css('display', 'none');
			}

			// Render the value component
			this._renderValueObject(fetchValuesUrl, renderFinishedCallback);
		},

		_renderValueObject: function(fetchValuesUrl, valueComponentRenderedCallback) {

			this.structure.value.show().empty();

			// Get the current selected condition object and its corresponding value component
			var selectedConditionObj =
                module.components.getConditionObject(this.property, this.currentPropertyType, this.currentCondition);
			var valueComponent = selectedConditionObj.valueComponent;

			if (!valueComponent) {

				// We don't have a value component to render, so we just call the valueComponentRendered callback.
				if (valueComponentRenderedCallback) {
					valueComponentRenderedCallback();
				}
			} else {

				// Generate the value component
				var componentGeneratorParams = {
					property: this.property,
					condition: selectedConditionObj,
					parentDiv: this.structure.value
				};
				var component = valueComponent.generator(componentGeneratorParams);

				// Append the value component
				this.structure.value.append(component);

				// Initialize packages
				this.structure.value.find('select').appseeSelect({
					container: module.globals.floatsContainer,
					size: module.globals.dropdownSize,
					classExtension: 'modified-bootstrap-select',
					replaceCaret: true
				});

				var self = this;
				var componentFullyInitializedCallback = function() {

					// Register change callback (if needed)
					if (valueComponent.registerChangeCallback) {

						// Register the change callback of the root element QB
						valueComponent.registerChangeCallback(component, function () {
							module.appseeQueryBuilder(module.globals.rootElement, 'queryBuilderStateChanged');
						});

						// Register the change callback given in the options (used for cascade steps)
						if (self.options.changeCallback) {
							valueComponent.registerChangeCallback(component, self.options.changeCallback);
						}
					}

					// Register the validation method
					valueComponent.registerValidationMethod(component, function() {
						self.validate();
					});

					// Set default value (if exists)
					var defaultValue = module.components.getDefaultValue(self.property, self.currentPropertyType,
						self.currentCondition);
					if (defaultValue !== null) {
						valueComponent.valueSetter(component, defaultValue);
					}

					// Fire the valueComponentRendered callback (if exists)
					if (valueComponentRenderedCallback) {
						valueComponentRenderedCallback();
					}
				};

				// Load values to the component (if needed) and then call setDefaultValueFunc
				if (valueComponent.valuesLoader) {

					// Get custom fetch values url (optional, therefore may be empty)
					const customFetchValuesUrl = fetchValuesUrl || this.customFetchValuesUrl;

					const transformer = this.property.valuesTransformer;

					// Load the values
					valueComponent.valuesLoader(component, customFetchValuesUrl, componentFullyInitializedCallback, transformer);
				} else {
					componentFullyInitializedCallback();
				}
			}
		},

		/******************
         * Public methods *
         ******************/

		getClassName: function () {
			return 'QueryTermFilter';
		},

		setDisabled: function (disabled) {

			// Change the value component disabled state by the given value
			var valueComponent =
                module.components.getValueComponent(this.property, this.currentPropertyType, this.currentCondition);
			if (valueComponent.setDisabled) {
				valueComponent.setDisabled(this.structure.value.find('.as-query-value-component'), disabled);
			}
		},

		setValue: function (value) {

			// Get the value setter function (by the property+condition)
			var valueSetter =
                module.components.getValueComponentSetter(this.property, this.currentPropertyType, this.currentCondition);

			// Set the value in the component
			if (valueSetter) {
				valueSetter(this.structure.value.find('.as-query-value-component'), value);
			}
		},

		getValue: function () {

			// Return the user input value
			var valueComponent =
                module.components.getValueComponent(this.property, this.currentPropertyType, this.currentCondition);
			return valueComponent.valueGetter(this.structure.value.find('.as-query-value-component'));
		},

		setState: function (state, setStateDoneCallback) {

			// Set the selected property type (if given)
			if ('propertyType' in state) {
				this._setSelectedPropertyType(state['propertyType']);
			}

			// Set the selected condition
			var self = this;
			this._setSelectedCondition(state['condition'],
				function () {

					// Set the value in the value component
					self.setValue(state['value']);

					// Call the set state done callback (if exists)
					if (setStateDoneCallback) {
						setStateDoneCallback();
					}
				});
		},

		render: function (fetchValuesUrl, renderFinishedCallback) {

			this.$element.empty()
				.append(this.structure.propertyType)
				.append(this.structure.condition)
				.append(this.structure.value);

			// Set the current property type (it may be changed by the user in case of type inferred)
			this.currentPropertyType = this.property.type;

			// Check if this property is type inferred (for example: custom user properties)
			if (this.property.isTypeInferred) {

				// Render the property types select
				this._renderPropertyTypes(fetchValuesUrl, renderFinishedCallback);
			} else {

				// Render the conditions select
				this._renderConditions(fetchValuesUrl, renderFinishedCallback);
			}
		},

		setCustomFetchValuesUrl: function (url) {
			this.customFetchValuesUrl = url;
		},

		validate: function () {

			// If property not rendered, like in case of TopicCompletionStatus
			if (this.$element[0].innerHTML === '') {
				return true;
			}

			// If no property type/condition is selected, return false
			if (!this.currentPropertyType || !this.currentCondition) {
				return false;
			}

			// Get the selected condition object and the user input value
			var selectedConditionObj =
                module.components.getConditionObject(this.property, this.currentPropertyType, this.currentCondition);
			var valueComponent = this.structure.value.find('.as-query-value-component');
			var value = valueComponent.length > 0
				? selectedConditionObj.valueComponent.valueGetter(valueComponent)
				: null;

			// Validate the value component
			return module.components.validateValueComponent(value, selectedConditionObj.validator, valueComponent);
		},

		generateJson: function () {

			// Property not rendered yet, it valid
			if (!this.currentPropertyType) {
				return {};
			}

			// Check if the query term filter is valid
			if (this.validate()) {

				// Get the selected condition object and the user input value
				var selectedConditionObj =
                    module.components.getConditionObject(this.property, this.currentPropertyType, this.currentCondition);
				var valueComponent = this.structure.value.find('.as-query-value-component');
				var value = valueComponent.length > 0
					? selectedConditionObj.valueComponent.valueGetter(valueComponent)
					: null;

				// Get the generated query of the term filter
				var termFilterQuery = selectedConditionObj.queryGen(this.property.name, value);

				// In case of type inferred property, we should save the chosen property type, in order to be able to
				// build the query builder again by the generated query
				if (this.property.isTypeInferred && this.property.name in termFilterQuery) {
					termFilterQuery[this.property.name]['extraInfo'] = { propertyType: this.currentPropertyType };
				}

				// We should create a custom properties (nested query) in case of custom property clause
				// (except 'is set'/'is not set' conditions)
				var createCustomPropertiesQuery = this.property.isCustom &&
                    $.inArray(this.currentCondition, ['is set', 'is not set']) === -1;

				// In case of custom property, return nested query, otherwise, return the term filter query
				return createCustomPropertiesQuery
					? { UserCustomProperties: { nested_match: { and: [termFilterQuery] } } }
					: termFilterQuery;

			} else {
				return {};
			}
		},

		countFilters: function () {
			return 1;
		}
	};

	module.QueryTermFilter = QueryTermFilter;

	return qb;

}($.QueryBuilder || {}));
