import {createCustomElement} from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';

import styles from './sn-appsee-collapsible-timeline.scss';

import '@servicenow/now-collapse';
import '@servicenow/now-icon';
import { t } from 'sn-translate';

const view = (state, { dispatch, updateState }) => {
	const { actions } = state.properties;
	const { iconMapping, expandedMapping } = state;

	// Render single action row
	// If action has props, render props collapsible section
	const renderSingleActionRow = (action, actionIndex, extraCssClasses) => {
		const actionHasProps = action.properties && Object.keys(action.properties).length > 0;

		const actionClickHandler = () => dispatch('SN_APPSEE_COLLAPSIBLE_TIMELINE#ACTION_ROW_CLICKED', { actionName: action.name, actionTime: action.time });

		return (
			<Fragment>
				<div role="row" className={`timeline-table__row ${extraCssClasses || ''}`}>
					<div role="cell" className="timeline-table__expand">
						{ actionHasProps ?  renderCollapseTrigger(actionIndex, action.name): null }
					</div>
					<div role="cell" title={action.name} aria-label={`Action - ${action.name}`} className="timeline-table__action" on-click={actionClickHandler}>
						<now-icon className="" icon={iconMapping[action.type]} size="md"></now-icon>
						<span className="timeline-table__action-name">{action.name}</span>
					</div>
					<div role="cell" aria-label={`Duration - ${action.time}`} className="timeline-table__time" on-click={actionClickHandler}>
						{action.time}
					</div>
				</div>

				{ actionHasProps ? renderPropsCollapseSection(action, actionIndex, action.properties) : null }
			</Fragment>
		);
	};


	// Render props collapsible section
	const renderPropsCollapseSection = (action, actionIndex, props) => {
		const actionPropId = `action${actionIndex}`;

		// Transform props object to match the renderSingleActionRow function
		const transformedProps = Object.entries(props).map(prop => {
			return {
				name: `${prop[0]}: ${prop[1]}`,
				time: action.time,
				type: 'prop'
			};
		});

		return (
			<now-collapse id={`collapse-${actionIndex}`} expanded={expandedMapping[actionPropId]}>
				{ transformedProps.map(prop => renderSingleActionRow(prop, actionIndex, 'timeline-table__row--indented')) }
			</now-collapse>
		);
	};

	// Render collapse trigger icon
	// This function uses expandedMapping state object to handle collapse/expand of each collapsible section
	const renderCollapseTrigger = (actionIndex, actionName) => {
		const actionPropId = `action${actionIndex}`;
		const actionIsExpanded = expandedMapping[actionPropId];
		const expandedIconName = iconMapping[actionIsExpanded ? 'expanded' : 'collapsed'];

		const changeIsExpanded = () => {
			updateState({
				expandedMapping: {
					...expandedMapping,
					[actionPropId]: actionIsExpanded === undefined ? true : !actionIsExpanded
				}
			});
		};

		// Support expand/collapse with enter key
		const onKeyupHandler = e => {
			if (e.which === 13) {
				changeIsExpanded();
			}
		};

		return (
			<div aria-controls={`collapse-${actionIndex}`} aria-expanded={expandedMapping[actionPropId]} aria-label={`collapsible area for ${actionName} properties`} tabindex="0" className="timeline-table__collapse-trigger" on-keyup={onKeyupHandler} on-click={changeIsExpanded}>
				<now-icon icon={expandedIconName} size="md"></now-icon>
			</div>
		);
	};

	return (
		<div role="table" className="timeline-table-wrapper">
			<div className="timeline-table__title">
				<div role="columnheader" className="timeline-table__title-cell timeline-table__title-cell--first-column">
					{ t('Action') }
				</div>
				<div role="columnheader" className="timeline-table__title-cell timeline-table__time">
					{ t('Duration') }
				</div>
			</div>
			<div role="rowgroup" className="timeline-table">
				{
					actions && actions.map((action, actionIndex) => renderSingleActionRow(action, actionIndex))
				}
			</div>
		</div>
	);
};

// A chat conversation actions table. Displays
// - Action name
// - Action time
// - Action props collapsible inner table
//
// <sn-va-collapsible-timeline
// 	actions={[
// 		{
// 			"name": "Activate account",
// 			"type": "Topic",
// 			"properties": {
// 				"Topic Id": "ID2",
// 				"Sub Category": "Setup",
// 				"Channel": "Slack",
// 				"Last Visited Node Name": "Confirm Email",
// 				"End State": "NOT Completed",
// 				"End Reason": "UserEnded",
// 				"Duration": 400
// 			},
// 			"time": 2000
// 		},
// 		{
// 			"name": "Activate account",
// 			"type": "Topic",
// 			"time": 5000
// 		},
// 	]]}
// ></sn-va-collapsible-timeline>

createCustomElement('sn-appsee-collapsible-timeline', {
	renderer: {type: snabbdom},
	view,
	properties: {
		// Actions data array.
		// type: `string`
		// default value: null
		// Required
		actions: {
			schema: {
				type: 'array',
				items: {
					name: 'string',
					type: 'string',
					time: 'number',
					properties: 'object'
				},
			},
			default: null,
			required: true
		}
	},
	initialState: {
		iconMapping: {
			Topic: 'chat-outline',
			prop: 'circle-dashed-fill',
			expanded: 'caret-down-fill',
			collapsed: 'caret-right-fill'
		},
		expandedMapping: {}
	},
	dispatches: {
		// Dispatches when an action row is clicked
		// @scheme: { actionName: string, actionTime: number }
		'SN_APPSEE_COLLAPSIBLE_TIMELINE#ACTION_ROW_CLICKED': {}
	},
	styles
});
