import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import './appsee-query-builder-plugin';
import { ACTIONS } from './constants';
import $ from 'jquery';
import appsee from './appsee-query-builder-plugin/appsee';
import { userFilterProperties, userFilterSessionExistsProperty, sessionExistsFilterProperties, userFilterType } from './filter-properties';

const view = (state, { dispatch }) => {
	const render = () => {
		let { defaultQuery, filterType, apiKey } = state.properties;

		const setQuery = (newQuery) => {
			dispatch(ACTIONS.QUERY_UPDATED, {query: newQuery});
		};

		let initialState = {
			apiKey: apiKey,
			filterType: filterType,
			query: defaultQuery,
			filterProperties: filterType === userFilterType ? userFilterProperties : sessionExistsFilterProperties
		};

		if (filterType === userFilterType) {
			initialState.sessionExistsProperty = userFilterSessionExistsProperty;
		}

		return (
			<sn-appsee-query-builder-plugin setQuery={setQuery} initialState={initialState}/>
		);
	};

	return render();
};

createCustomElement('sn-appsee-query-builder', {
	renderer: {type: snabbdom},
	view,
	properties: {
		defaultQuery: {
			default: '{"and": []}'
		},
		filterType: {
			default: userFilterType
		},
		apiKey: {
			default: ''
		}
	}
});
