import appseeUtils from './appseeUtils';
import appsee from './appsee';
import $ from 'jquery';
import moment from 'moment';
import daterangepicker from './daterangepicker';
import { t } from 'sn-translate';

$.QueryBuilder = function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	module.internalUtils = {

		platformsEnum: {
			'0': 'iOS',
			'1': 'Android',
			'2': 'Web',
			'3': 'Chat'
		},

		filterShowOnlyInContext: function (item) {
			if (item.showOnlyInContext) {
				for (const key in item.showOnlyInContext) {
					if (item.showOnlyInContext.hasOwnProperty(key)) {

						// Check if the context value of the current key doesn't exist in the required context values
						const keyFromContext = key in module.globals.context ? module.globals.context[key] : 'all';
						if (item.showOnlyInContext[key].indexOf(keyFromContext) === -1) {

							// If the item didn't pass the condition, return false
							return false;
						}
					}
				}
			}

			// The item is okay
			return true;
		},

		getPlatformEnumValueByName: function (platformName) {
			var platformsEnum = this.platformsEnum;

			for (var key in platformsEnum) {
				if (platformsEnum.hasOwnProperty(key)) {
					if (platformsEnum[key] === platformName) {
						return parseInt(key);
					}
				}
			}

			return null;
		},

		performAjaxCall: function (url, successCallback, errorCallback) {
			successCallback = successCallback || $.noop;
			errorCallback = errorCallback || $.noop;


			var params = {appFriendlyName: appsee.context.app, api_key: appsee.context.apiKey};

			if (appsee.context.platform !== 'all') {
				params.platform = appsee.context.platform;
			}

			// Check if we have params in the url
			var urlParts = url.split('?');
			if (urlParts.length > 1 && urlParts[1] !== '') {
				var paramsDict = {};

				// Add all the params to the params dictionary
				var paramsArr = urlParts[1].split('&');
				for (var i = 0; i < paramsArr.length; i++) {
					var paramParts = paramsArr[i].split('=');
					paramsDict[paramParts[0]] = paramParts[1];
				}

				params = $.extend({}, params, paramsDict);
			}

			$.ajax({
				type: 'POST',
				headers: {
					'Content-Type': 'application/json'
				},
				url: urlParts[0],
				data: JSON.stringify(params),
				success: successCallback,
				error: errorCallback
			});
		},

		createLoader: function () {
			return $('<div class="as-loader-container"><div class="ajax-loader-small" /><span>Loading...</span></div>').hide();
		},

		tryParseJSON: function (jsonString) {
			try {
				var obj = JSON.parse(jsonString);

				// Handle non-exception-throwing cases:
				// Neither JSON.parse(false) or JSON.parse(1234) throw errors, hence the type-checking,
				// but... JSON.parse(null) returns null, and typeof null === "object",
				// so we must check for that, too. Thankfully, null is falsey, so this suffices:
				if (obj && typeof obj === 'object') {
					return obj;
				}
			} catch (e) {}

			return false;
		},

		arraysEqual: function (a, b) {
			if (a === b) return true;
			if (a == null || b == null) return false;
			if (a.length !== b.length) return false;

			// We don't care about the order of the elements inside the arrays, so we should sort both arrays
			// before comparing the elements
			var copyA = a.slice().sort();
			var copyB = b.slice().sort();

			for (var i = 0; i < copyA.length; ++i) {
				if (copyA[i] !== copyB[i]) return false;
			}
			return true;
		},

		wrapQueryWithRelation: function (query, relation) {

			// Determine the relation key according to the given relation
			var relationKey;
			switch (relation) {
				case 'child':
					relationKey = 'child_match';
					break;
				case 'parent':
					relationKey = 'parent_match';
					break;
				case 'nestedPlatform':
				case 'nested':
					relationKey = 'nested_match';
					break;
				default:
					relationKey = null;
			}

			// Wrap the query with the relation key (if exists)
			if (relationKey) {
				var newQuery = {};
				newQuery[relationKey] = query;
				query = newQuery;
			}

			return query;
		},

		wrapQueryWithPropertyName: function (query, propertyName) {
			var newQuery = {};
			newQuery[propertyName] = query;
			return newQuery;
		},

		createDatePicker: function (isRangePicker, parentDiv) {

			// Set the common options for single and range date pickers
			var options = {
				singleDatePicker: !isRangePicker,
				showDropdowns: false,
				opens: 'center',
				applyClass: 'green',
				maxDate: moment().utc(),
				autoUpdateInput: false,
				parentEl: parentDiv
			};

			if (module.globals.floatsContainer) {
				options.parentEl = module.globals.floatsContainer;
			} else {
				// Modify the class of the parent div (in order to recognize which datepicker we have inside, and set its width accordingly)
				var datePickerClass = isRangePicker ? 'range-picker' : 'single-picker';
				$(parentDiv).removeClass('range-picker').removeClass('single-picker').addClass(datePickerClass);
			}

			var placeholder = t('Select Date');

			// Add the relevant options in case of range picker
			if (isRangePicker) {
				$.extend(options, {
					linkedCalendars: false,
					alwaysShowCalendars: true,
					showCustomRangeLabel: false,
					showDropdowns: false,
					ranges: {
						'Today': appsee.calculateRelativeLabelDates('Today'),
						'Yesterday': appsee.calculateRelativeLabelDates('Yesterday'),
						'Last 7 Days': appsee.calculateRelativeLabelDates('Last 7 Days'),
						'Last 30 Days': appsee.calculateRelativeLabelDates('Last 30 Days'),
						'Last 90 Days': appsee.calculateRelativeLabelDates('Last 90 Days'),
						'This Month': appsee.calculateRelativeLabelDates('This Month')
					},
					locale: {
						applyLabel: t('Select'),
						cancelLabel: t('Cancel'),
						customRangeLabel: t('Custom Range'),
						// This object holds translations for ranges names and 
						// !!!!! MUST be synchronized with ^ ranges object
						ranges: {
							'Today': t('Today'),
							'Yesterday': t('Yesterday'),
							'Last 7 Days': t('Last 7 Days'),
							'Last 30 Days': t('Last 30 Days'),
							'Last 90 Days': t('Last 90 Days'),
							'This Month': t('This Month')
						},
						description: t('Each day starts at midnight UTC')
					}
				});

				placeholder += 's';
			}

			// Create the date picker
			var element = $('<input type="text" readOnly="true" class="as-query-value-component as-query-value-date-picker m-wrap" />').attr('placeholder', placeholder);
			element.daterangepicker(options, module.internalUtils.getDatePickerApplyCallback(isRangePicker, element));

			if (module.globals.floatsContainer) {
				var guid = appseeUtils.guid();
				// In order to make daterangepicker aligned with its input while scrolling, when the plug-in is created with parentEl
				// daterangepicker.move knows to move the content to be aligned,remo so we call it on each scroll event
				$(window).on('scroll.appsee.date.' + guid, function () {
					const datepickerObject = element.data('daterangepicker');
					// The picker might already be destroyed by the time we get here (because of a "Reset" click)
					if (datepickerObject) {
						datepickerObject.move();
					}
				});

				// Handle element deletion from DOM
				element.on('remove', function () {
					var datepickerObject = $(this).data('daterangepicker');

					// Not stucking the UI thread
					setTimeout(function () {

						// Remove redundant handler from window
						$(window).off('.appsee.date.' + guid);

						// Remove the dropdown container from it's parent DOM
						datepickerObject.remove();
					}, 0);
				});
			}

			return element;
		},

		getDatePickerApplyCallback: function (isRangePicker, element) {
			if (isRangePicker) {
				return function (start, end, label) {
					$(element).val(start.format('MM/DD/YYYY') + ' - ' + end.format('MM/DD/YYYY'));
				};
			} else {
				return function (start, end, label) {
					$(element).val(start.format('MM/DD/YYYY'));
				};
			}
		},

		// Options are: {description: string, selectClassName: string, autoComplete: bool, defaultSelected: bool}
		generateDropdown: function (name, items, options) {
			options = options || {};

			var $select = $('<select/>').addClass('as-query-dropdown').attr('name', name);

			if ('description' in options) {
				$select.attr('title', options.description);
			}

			if ('selectClassName' in options) {
				$select.addClass(options.selectClassName);
			}

			if (options.autoComplete) {
				$select.attr('data-live-search', 'true');
			}

			// Loading the select values from the fetchValuesUrl, if exists (asynchronous)
			if (typeof items === 'string' || items instanceof String) {
				$select.attr('data-fetch-url', items);
			} else {

				// Populate dropdown items
				var itemValueAccessor = Array.isArray(items) ? function (x) {
					return items[x];
				} : function (x) {
					return x;
				};
				var defaultSelected = false;
				for (var item in items) {
					var itemValue = itemValueAccessor(item);
					var itemText = items[item];
					var $option = $('<option class="as-query-dropdown-item"></option>').val(itemValue).text(itemText);

					// If no default was selected yet and this is required, select the option as default
					if (!defaultSelected && options.defaultSelected) {
						$option.prop('selected', true);
						defaultSelected = true;
					}

					$select.append($option);
				}
			}
			return $select;
		}

	};

	return qb;

}($.QueryBuilder || {});
