import $ from 'jquery';
import appseeUtils from './appseeUtils';
import { t } from 'sn-translate';

(function ($) {

	var AppseeSearchCoordinator = function (element, options) {
		this._init(element, options);
	};

	AppseeSearchCoordinator.prototype = {

		defaultOptions: {
			appseeQueryBuilderContext: {},
			appseeQueryBuilderElement: null,
			appseeQueryBuilderParser: null,
			setQuery: null,
			initialState: { query: '{"and": []}' },
			invalidQueryCallback: $.noop,
		},

		constructor: AppseeSearchCoordinator,

		_init: function (element, options) {
			this.$element = $(element);
			this.settings = $.extend({}, this.defaultOptions, options);

			this._searchHandler = this._searchHandler.bind(this);
			this.reset = this.reset.bind(this);

			var $searchButtonContainer = $('<div class="btn-container"></div>');

			var $searchButton =
				$('<button class="btn coordinator-btn-save apply-btn" type="button">' + t('Apply') + '</button>')
					.click(this._searchHandler);

			var $resetButton =
				$('<button class="btn coordinator-btn-reset cancel-btn" type="button">' + t('Reset') + '</button>')
					.click(this.reset);

			$searchButtonContainer.append($searchButton).append($resetButton);

			this.$element.addClass('as-search-coordinator').append($searchButtonContainer);

			// Set the given initial state
			this._setInitialState(this.settings.initialState, false);
		},

		reset: function() {
			this._setState({ query: this.settings.initialState.query }, true);
			this.settings.appseeQueryBuilderElement.appseeQueryBuilder('setState', this._getQueryBuilderStateByQuery(this.query));
		},

		_getQueryBuilderStateByQuery: function (query) {
			return this.settings.appseeQueryBuilderParser.jsonQueryStringToState(query,
				this.settings.appseeQueryBuilderElement.appseeQueryBuilder('getProperties'));
		},

		_setState: function (state, shouldNotifyTableStateChanged = true) {
			this.query = state.query || this.settings.initialState.query;
			if (shouldNotifyTableStateChanged) {
				if (this.settings.setQuery)
					this.settings.setQuery(this.query);
			}
		},

		_setInitialState: function (initialState) {
			// Url should be prior to any sent parameters
			// Overriding settings defaults in order for 'reset' button to reset the state to the initial state the
			// coordinator started with
			this.settings.initialState.query = initialState.query || this.settings.initialState.query;

			this._setState(initialState, false);

			// Set the query into the Query Builder
			this.settings.appseeQueryBuilderElement.appseeQueryBuilder('setState', this._getQueryBuilderStateByQuery(this.query));
		},

		_searchHandler: function () {
			if (this.settings.appseeQueryBuilderElement.appseeQueryBuilder('isValid')) {
				this._setState({
					query: this.settings.appseeQueryBuilderElement.appseeQueryBuilder('getState')
				});
			} else {
				this.settings.invalidQueryCallback();
			}
		}
	};

	$.fn.appseeSearchCoordinator = function (option) {
		var args = Array.prototype.slice.call(arguments);
		return appseeUtils.createClassInstance(this, 'as-search-coordinator', AppseeSearchCoordinator, option, args);
	};

})($);

