import { t } from 'sn-translate';

const validators = {
	'noop': {
		accessor: function (ignored) { return true; }
	},
	'not-empty': {
		msg: t('Please enter a value'),
		accessor: function (value) { return Boolean(value); }
	},
	'number': {
		msg: t('Please enter a valid number'),
		accessor: function (value) { return Boolean(!isNaN(value)); }
	},
	'number-range': {
		msg: t('Please enter valid numbers'),
		accessor: function (value) { return validators['number'].accessor(value.start) && validators['number'].accessor(value.end) && (value.start <= value.end); }
	},
	'date': {
		msg: t('Please select a date'),
		accessor: function (value) { return Boolean(value); }
	},
	'date-range': {
		msg: t('Please select dates'),
		accessor: function (value) { return validators['date'].accessor(value); }
	},
	'date-relative': {
		msg: t('Please enter a value'),
		accessor: function (value) {
			const MAX_DAYS = 730;
			const MAX_WEEKS = 104;
			const MAX_MONTHS = 24;

			this.msg = t('Please enter a value');
			const val = value.value;

			if (!(validators['number'].accessor(val) && (val > 0) &&
                    validators['value-selected'].accessor(value.timeUnit))) {
				return false;
			}

			switch (value.timeUnit) {
				case 'days':
					if (val > MAX_DAYS) {
						this.msg = t('Days value must be between 1 and {0}', MAX_DAYS);
						return false;
					}
					break;
				case 'weeks':
					if (val > MAX_WEEKS) {
						this.msg = t('Weeks value must be between 1 and {0}', MAX_WEEKS);
						return false;
					}
					break;
				case 'months':
					if (val > MAX_MONTHS) {
						this.msg = t('Months value must be between 1 and {0}', MAX_MONTHS);
						return false;
					}
					break;
			}

			return true;
		}
	},
	'value-selected': {
		msg: t('Please select a value'),
		accessor: function (value) {
			if (Array.isArray(value))
				return value.length > 0;
			return value != null && value !== '';
		}
	},
	'duration': {
		msg: t('Please enter a valid duration'),
		accessor: function (value) {
			return (validators['number'].accessor(value) && value >= 0);
		}
	},
	'duration-range': {
		msg: t('Please enter a valid range'),
		accessor: function (value) { return validators['duration'].accessor(value.start) && validators['duration'].accessor(value.end) && (value.end >= value.start); }
	},
	'unbound-list-not-empty': {
		msg: t('Please enter a value'),
		accessor: function (value) { return Boolean(value.length > 0); }
	}
};

export default validators;
