import './value-components';
import valueHelper from './value-helper';

import $ from 'jquery';
import moment from 'moment';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	module.typesDictionary = {

		string: {
			description: t('Text'),
			conditions: [
				{ name: t('equals to'), valueComponent: module.valueComponents.stringInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { eq: value }; }), validator: 'not-empty' },
				{ name: t('not equals to'), valueComponent: module.valueComponents.stringInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { neq: value }; }), validator: 'not-empty' },
				{ name: t('contains'), valueComponent: module.valueComponents.stringInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { contains: value }; }), validator: 'not-empty' }
			],
			isTypeInferredSupported: true
		},
		number: {
			description: t('Number'),
			conditions: [
				{ name: '=', valueComponent: module.valueComponents.numberInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { eq: value }; }), validator: 'number' },
				{ name: '>', valueComponent: module.valueComponents.numberInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gt: value }; }), validator: 'number' },
				{ name: '>=', valueComponent: module.valueComponents.numberInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gte: value }; }), validator: 'number' },
				{ name: '<', valueComponent: module.valueComponents.numberInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { lt: value }; }), validator: 'number' },
				{ name: '<=', valueComponent: module.valueComponents.numberInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { lte: value }; }), validator: 'number' },
				{ name: t('is between'), valueComponent: module.valueComponents.numberRange, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gte: value.start, lte: value.end }; }), validator: 'number-range' }
			],
			isTypeInferredSupported: true
		},
		boolean: {
			description: t('Yes/No'),
			conditions: [
				{ name: t('is'), valueComponent: module.valueComponents.booleanInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { eq: value }; }), validator: 'value-selected' }
			],
			isTypeInferredSupported: true
		},
		existenceBasedBoolean: {
			description: t('Yes/No'),
			conditions: [
				{ name: t('is'), valueComponent: module.valueComponents.booleanInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return value ? { 'exists': true } : { 'not exists': true }; }), validator: 'value-selected' }
			]
		},
		date: {
			description: t('Date'),
			conditions: [
				{ name: t('is in the last'), valueComponent: module.valueComponents.dateRelative, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { in_last: value }; }), validator: 'date-relative' },
				{ name: t('is on'), valueComponent: module.valueComponents.datePicker, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gte: valueHelper.getFormattedDate(moment.utc(value).startOf('day')), lte: valueHelper.getFormattedDate(moment.utc(value).endOf('day')) }; }), validator: 'date' },
				{ name: t('is later than'), valueComponent: module.valueComponents.datePicker, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gt: valueHelper.getFormattedDate(moment.utc(value).endOf('day')) }; }), validator: 'date' },
				{ name: t('is earlier than'), valueComponent: module.valueComponents.datePicker, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { lt: valueHelper.getFormattedDate(moment.utc(value).startOf('day')) }; }), validator: 'date' },
				{ name: t('is in range'), valueComponent: module.valueComponents.dateRange, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gte: valueHelper.getFormattedDate(moment.utc(value.startDate).startOf('day')), lte: valueHelper.getFormattedDate(moment.utc(value.endDate).endOf('day')) }; }), validator: 'date-range' }
			],
			isTypeInferredSupported: true
		},
		list: {
			description: t('List'),
			conditions: [
				{ name: t('contain any of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { contains_any: value }; }), validator: 'value-selected' },
				{ name: t('contain all of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { contains_all: value }; }), validator: 'value-selected' }
			]
		},
		optionalList: {
			description: t('List'),
			conditions: [
				{ name: t('any'), valueComponent: module.valueComponents.hiddenValue, queryGen: valueHelper.getQueryGenWrapper(function (ignored) { return null; }), validator: 'noop' },
				{ name: t('contain any of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return value ? { contains_any: value } : value; }), validator: 'value-selected' },
				{ name: t('contain all of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return value ? { contains_all: value } : value; }), validator: 'value-selected' }
			]
		},
		unboundList: {
			description: t('List'),
			conditions: [
				{ name: t('contain any of'), valueComponent: module.valueComponents.unboundListInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { contains_any: value }; }), validator: 'unbound-list-not-empty' },
				{ name: t('contain all of'), valueComponent: module.valueComponents.unboundListInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { contains_all: value }; }), validator: 'unbound-list-not-empty' }
			],
			isTypeInferredSupported: true
		},
		bound: {
			description: t('List'),
			conditions: [
				{ name: t('is any of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { is_any: value }; }), validator: 'value-selected' },
				{ name: t('is none of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { is_not_any: value }; }), validator: 'value-selected' }
			]
		},
		optionalBound: {
			description: t('List'),
			conditions: [
				{ name: t('any'), valueComponent: module.valueComponents.hiddenValue, queryGen: valueHelper.getQueryGenWrapper(function (ignored) { return null; }), validator: 'noop' },
				{ name: t('is any of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { is_any: value }; }), validator: 'value-selected' },
				{ name: t('is none of'), valueComponent: module.valueComponents.boundSelect, componentParams: { multiple: true }, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { is_not_any: value }; }), validator: 'value-selected' }
			]
		},
		duration: {
			description: t('Duration'),
			conditions: [
				{ name: '>', valueComponent: module.valueComponents.durationInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gt: valueHelper.getDurationInMillis(value) }; }), validator: 'duration' },
				{ name: '>=', valueComponent: module.valueComponents.durationInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gte: valueHelper.getDurationInMillis(value) }; }), validator: 'duration' },
				{ name: '<', valueComponent: module.valueComponents.durationInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { lt: valueHelper.getDurationInMillis(value) }; }), validator: 'duration' },
				{ name: '<=', valueComponent: module.valueComponents.durationInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { lte: valueHelper.getDurationInMillis(value) }; }), validator: 'duration' },
				{ name: t('is between'), valueComponent: module.valueComponents.durationRange, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { gte: valueHelper.getDurationInMillis(value.start), lte: valueHelper.getDurationInMillis(value.end) }; }), validator: 'duration-range' }
			]
		},
		actionKeys: {
			description: t('Actions'),
			conditions: [
				{ name: t('is'), valueComponent: module.valueComponents.boundActions, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { eq: value }; }), validator: 'value-selected' }
			]
		},
		hidden: {
			description: t('Hidden Value'),
			conditions: [
				{ name: '', valueComponent: module.valueComponents.hiddenValue, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { eq: value }; }), validator: 'noop' }
			]
		},
		platform: {
			description: t('Platform'),
			conditions: [
				{ name: t('is'), valueComponent: module.valueComponents.platformInput, queryGen: valueHelper.getQueryGenWrapper(function (value) { return { eq: value }; }), validator: 'value-selected' }
			]
		}
	};

	return qb;

}($.QueryBuilder || {}));
