import appseeUtils from './appseeUtils';
import './internal-utils';
import $ from 'jquery';
import './appsee-actions-grid';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var ActionSelector = function (element, options) {
		this._init(element, options);
	};

	ActionSelector.prototype = {

		_defaultOptions: {
			changeCallback: $.noop,
			fetchUrl: null,
			targetColor: '#18bcf2',
			invertColor: '#ffffff'
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);

			this._showContainer = this._showContainer.bind(this);
			this._moveContainer = this._moveContainer.bind(this);
			this._actionSelected = this._actionSelected.bind(this);

			this.$element.addClass('as-action-selector');

			// Init loader
			this.$loader = module.internalUtils.createLoader();
			this._showLoader(false);
			this.$element.append(this.$loader);

			var self = this;

			// Init select action button
			this.$button =
                $('<button type="button" class="as-action-selector__button"></button>')
                	.append('<span class="as-action-selector__buttonText">Select Element</span>&nbsp;')
                	.append('<span class="bs-caret"><span class="appsee-icon_down"></span></span>')
                	.click(function (e) {
                		self._showContainer(!self.isShown);
                	});

			this._showComponent(false);
			this.$element.append(this.$button);

			// Init actions grid and it's container
			this.$actionsGrid = $('<div class="grid-container__actions" />').appseeActionsGrid({
				actionSelectedCallback: this._actionSelected,
				actionNotSelectedCallback: function () {
					self.$button.find('.as-action-selector__buttonText').text('Select Element');
				},
				spaceItemsAround: true
			});

			this.$gridContainer = $('<div class="as-action-selector grid-container" />');
			this.$gridContainer.append(this.$actionsGrid);
			this._showContainer(false);
			$(module.globals.floatsContainer).append(this.$gridContainer);

			// Register events
			var guid = appseeUtils.guid();

			$(window).on('scroll.appsee.actionSelector.' + guid, this._moveContainer)
				.on('resize.appsee.actionSelector.' + guid, this._moveContainer);

			//$(document).on("click.appsee.actionSelector." + guid, this._showContainer.bind(this, false))
			$(document).on('click.appsee.actionSelector.' + guid, this._outsideClick.bind(this))
			// Bind global datepicker mousedown for hiding and
				.on('mouseup.appsee.actionSelector.' + guid, this._outsideClick.bind(this))
			// also support mobile devices
				.on('touchend.appsee.actionSelector.' + guid, this._outsideClick.bind(this))
			// and also close when focus changes to outside the picker (eg. tabbing between controls)
				.on('focusin.appsee.actionSelector.' + guid, this._outsideClick.bind(this));

			// Handle component deletion from DOM
			this.$element.on('remove', function () {

				// Not stucking the UI thread
				setTimeout(function () {

					// Remove redundant handler from window
					$(window).off('.appsee.actionSelector.' + guid);
					$(document).off('.appsee.actionSelector.' + guid);

					// Remove the dropdown container from it's parent DOM
					self.$gridContainer.remove();
				}, 0);
			});

			if (this.options.fetchUrl)
				this._addItemsFromUrlAsync(this.options.fetchUrl);
		},

		_addItems: function (items, valuesLoadedCallback) {
			this.$actionsGrid.appseeActionsGrid('clearPresentedActions');
			this.$actionsGrid.appseeActionsGrid('addActionsToGrid', items);

			// Auto select action if only one exists and hide id it's 'navigation back'
			this.$actionsGrid.appseeActionsGrid('handleSingleAction', this.actionType);

			if (valuesLoadedCallback) {
				valuesLoadedCallback();
			}
		},

		_addItemsFromUrlAsync: function (fetchUrl, valuesLoadedCallback) {
			if (!fetchUrl)
				return;

			this._showComponent(false);
			this._showLoader(true);

			var self = this;
			var successFunc = function (data) {
				if (data != null) {
					self._showLoader(false);
					self._showComponent(true);
					self._addItems(data, valuesLoadedCallback);
				}
			};

			var errorFunc = function (data) {
				self.$element.empty();
				self.$element.append('<span class="as-query-fetch-error-msg">' + t('Error fetching actions') + '<span/>');
			};

			// Regex to capture actionType from the url (also supports negative values)
			// [1] is used to get the actual value (first index is the whole string (ie: "actionType=3")
			this.actionType = parseInt(fetchUrl.match(/[?&]actionType=(-?\d+)/)[1]);

			// Add colors for ActionsGrid
			fetchUrl += '&targetColor=' + this.options.targetColor + '&invertColor=' + this.options.invertColor;

			// Load values
			module.internalUtils.performAjaxCall(fetchUrl, successFunc, errorFunc);
		},

		_actionSelected: function (userInitiated) {
			this.$button.find('.as-action-selector__buttonText').text('Element Selected');

			// If it's not called by the user, that means automatic default choice was made for the user
			// and the user does not need to see the selection
			if (!userInitiated) {
				this.$button.hide();
			}

			this._showContainer(false);
		},

		_showLoader: function (show) {
			if (show) {
				this.$loader.fadeIn('slow');
			} else {
				this.$loader.hide();
			}
		},

		_showComponent: function (show) {
			if (show) {
				this.$button.show();
			} else {
				this.$button.hide();
			}
		},

		_showContainer: function (show) {
			if (show) {
				this.$gridContainer.show();
				this.isShown = true;
				this._moveContainer();
			} else {
				this.$gridContainer.hide();
				this.isShown = false;
				if (this.isShown) {
					this.$element.trigger('hide.appsee.actionSelector', this);
				}
			}
		},

		_moveContainer: function () {
			if (this.isShown) {
				var parentOffset = { top: 0, left: 0 };
				var $parentEl = $(module.globals.floatsContainer);

				if (!$parentEl.is('body')) {
					parentOffset = {
						top: $parentEl.offset().top - $parentEl.scrollTop(),
						left: $parentEl.offset().left - $parentEl.scrollLeft()
					};
				}

				var containerTop = this.$element.offset().top + this.$element.outerHeight() - parentOffset.top;

				// TBD - may be remove it
				containerTop += 80;

				this.$gridContainer.css({
					top: containerTop,
					left: this.$element.offset().left - parentOffset.left + this.$element.outerWidth() / 2
                    - this.$gridContainer.outerWidth() / 2,
					right: 'auto'
				});

				if (this.$gridContainer.offset().left < 0) {
					this.$gridContainer.css({
						right: 'auto',
						left: 9
					});
				}
			}
		},

		_outsideClick: function (e) {
			// e.target is wrong in dashboard seismic version let's use e.originalEvent.path[0] as source event element
			const target = e.originalEvent.path ? $(e.originalEvent.path[0]) : e.originalEvent.currentTarget;

			// if the page is clicked anywhere except within the actionSelector/button
			// itself then call this._showContainer(false)
			if (target.closest(this.$element).length || target.closest(this.$gridContainer).length) {
				return;
			}
			this._showContainer(false);
		},

		/******************
         * Public methods *
         ******************/

		fetch: function (fetchUrl, valuesLoadedCallback) {
			this._addItemsFromUrlAsync(fetchUrl, valuesLoadedCallback);
		},

		setVal: function (value) {
			this.$actionsGrid.appseeActionsGrid('selectAction', value, true);
		},

		getVal: function () {
			var action = this.$actionsGrid.appseeActionsGrid('getSelectedAction');
			if (action) {
				return action.ActionKey;
			} else {
				return '';
			}
		}
	};

	module.actionSelector = function (elements, option) {
		var args = Array.prototype.slice.call(arguments).splice(1);
		return appseeUtils.createClassInstance(elements, 'as-action-selector', ActionSelector, option, args);
	};

	return qb;

}($.QueryBuilder || {}));
