import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import './sn-custom-event-chart';
import styles from './styles.scss';
import {
	SN_CUSTOM_EVENT_CHART_PROPERTY_VIEW_CHANGED,
	SN_CUSTOM_EVENT_CHART_DATE_BUCKET_CHANGED,
	SN_CUSTOM_EVENT_CHART_DOWNLOAD_CLICKED,
	SN_CUSTOM_EVENT_CHART_VISUALIZATION_CLICKED,
	SN_VA_CUSTOM_EVENT_PROPS_PROPERTY_VIEW_CHANGED,
	SN_VA_CUSTOM_EVENT_PROPS_DOWNLOAD_CLICKED,
	SN_VA_CUSTOM_EVENT_PROPS_VISUALIZATION_CLICKED,
	SN_VA_CUSTOM_EVENT_PROPS_DATE_BUCKET_CHANGED
} from './constants';

const view = (state, {updateState}) => {
	const {
		properties: {
			items
		}
	} = state;
	return (
		<div className="sn-va-custom-event-props-wrapper">
			{
				items.map(item => (
					<sn-custom-event-chart {...item} />
				))
			}
		</div>
	);
};

createCustomElement('sn-va-custom-event-props', {
	renderer: {type: snabbdom},
	view,
	properties: {
		items: {
			default: []
		}
	},
	actionHandlers: {
		[SN_CUSTOM_EVENT_CHART_PROPERTY_VIEW_CHANGED]: ({ action, dispatch }) => {
			const { id: itemId, selectedView } = action.payload;
			dispatch(SN_VA_CUSTOM_EVENT_PROPS_PROPERTY_VIEW_CHANGED, { itemId, selectedView });
		},
		[SN_CUSTOM_EVENT_CHART_DOWNLOAD_CLICKED]: ({ action, dispatch }) => {
			const { id: itemId } = action.payload;
			dispatch(SN_VA_CUSTOM_EVENT_PROPS_DOWNLOAD_CLICKED, { itemId });
		},
		[SN_CUSTOM_EVENT_CHART_DATE_BUCKET_CHANGED]: ({ action, dispatch }) => {
			const { id: itemId, selectedBucket } = action.payload;
			dispatch(SN_VA_CUSTOM_EVENT_PROPS_DATE_BUCKET_CHANGED, { itemId, selectedBucket });
		},
		[SN_CUSTOM_EVENT_CHART_VISUALIZATION_CLICKED]: ({ action, dispatch }) => {
			const { id: itemId, parPayload } = action.payload;
			dispatch(SN_VA_CUSTOM_EVENT_PROPS_VISUALIZATION_CLICKED, { itemId, parPayload });
		}
	},
	dispatches: {
		[SN_VA_CUSTOM_EVENT_PROPS_PROPERTY_VIEW_CHANGED]: {
			schema: {
				type: 'object',
				properties: {
					id: { type: 'string' },
					selectedView: { type: 'string' }
				}
			}
		},
		[SN_VA_CUSTOM_EVENT_PROPS_DOWNLOAD_CLICKED]: {
			schema: {
				type: 'object',
				properties: {
					id: { type: 'string' }
				}
			}
		},
		[SN_VA_CUSTOM_EVENT_PROPS_DATE_BUCKET_CHANGED]: {
			schema: {
				type: 'object',
				properties: {
					id: { type: 'string' },
					selectedBucket: { type: 'string' }
				}
			}
		},
		[SN_VA_CUSTOM_EVENT_PROPS_VISUALIZATION_CLICKED]: {
			schema: {
				type: 'object',
				properties: {
					id: { type: 'string' },
					parPayload: { type: 'object' }
				}
			}
		},
	},
	styles
});
