import $ from 'jquery';
import appseeUtils from './appseeUtils';
import './appsee-select';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryClause = function (element, options) {
		this._init(element, options);
	};

	QueryClause.prototype = {

		_defaultOptions: {
			properties: null,
			propertyWasSelected: $.noop
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);

			this.properties = this.options.properties;

			this.structure = {
				property: $('<div/>').addClass('as-query-property'),
				propertyFilter: $('<div/>').addClass('as-query-property-filter')
			};

			this.$element.empty().addClass('as-query-clause')
				.append(this.structure.property)
				.append(this.structure.propertyFilter);

			this._render();
		},

		_getPropertyName: function () {
			return this.structure.property.find('select.as-query-property-select').val();
		},

		_render: function () {
			var $propertySelect = this.properties.generatePropertySelect();

			var self = this;
			$propertySelect.on('changed.bs.select', function () {
				self._propertySelectChanged($(this).val());
				self.options.propertyWasSelected();
			});

			// Append the select to the elements and only then initiate appseeSelect (won't work otherwise)
			this.structure.property.empty().append($propertySelect);

			$propertySelect.appseeSelect({
				container: module.globals.floatsContainer,
				size: module.globals.dropdownSize,
				classExtension: 'modified-bootstrap-select query-clause-select',
				replaceCaret: true
			});
		},

		_createPropertyFilter: function (propertyName, propertyFilterRendered) {

			// Removing the previous data from the DOM element so we can init the div again but keeping the DOM element
			this.structure.propertyFilter.empty().removeData();

			var PropertyFilterClass = this.properties.getPropertyFilter(propertyName);
			var options = { property: this.properties.getProperty(propertyName) };

			// When creating internal QB, we set the empty notification callback to remove the clause
			if (PropertyFilterClass === module.QueryBuilderFilter) {
				options['emptyNotificationCallback'] = this.options.removeClauseCallback;
			}

			// Initialize the property filter component
			module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, options);

			// Render the property filter according to its type
			if (PropertyFilterClass === module.QueryTermFilter) {
				module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'render', null, propertyFilterRendered);
			} else if (PropertyFilterClass === module.QueryCascadeFilter) {
				module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'render', propertyFilterRendered);
			} else if (PropertyFilterClass === module.QueryBuilderFilter) {
				module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'fetchPropertiesAndRender', null, propertyFilterRendered);
			}
		},

		_propertySelectChanged: function (selectedProperty, propertySelectedCallback) {

			// Create the property filter
			this._createPropertyFilter(selectedProperty, propertySelectedCallback);
		},

		_setSelectedProperty: function (property, propertySelectedCallback) {
			var $propertySelect = this.structure.property.find('select.as-query-property-select.as-select');
			$propertySelect.val(property).appseeSelect('refresh');
			this._propertySelectChanged(property, propertySelectedCallback);
		},

		/******************
         * Public methods *
         ******************/

		setState: function (state, stateSyncDoneCallback) {

			// Select the property
			var self = this;
			this._setSelectedProperty(state['property'], function () {

				// Set the state for the property filter
				var propertyFilterClass = self.properties.getPropertyFilter(state['property']);
				module.queryPropertyFilter(self.structure.propertyFilter, propertyFilterClass, 'setState',
					state['propertyFilterState'], stateSyncDoneCallback);
			});
		},

		validate: function () {
			var PropertyFilterClass = this.properties.getPropertyFilter(this._getPropertyName());

			// If we find unselected property it is valid
			if (PropertyFilterClass == null) {
				return true;
			} else {
				return module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'validate');
			}
		},

		generateJson: function () {
			var PropertyFilterClass = this.properties.getPropertyFilter(this._getPropertyName());

			// If we find unselected property return an empty json
			if (PropertyFilterClass == null) {
				return {};
			}

			return module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'generateJson');
		},

		countFilters: function () {
			var PropertyFilterClass = this.properties.getPropertyFilter(this._getPropertyName());

			// If we find unselected property we count it as 1
			if (PropertyFilterClass == null) {
				return 1;
			} else {
				return module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'countFilters');
			}
		},

		indentNestedQueryBuilders: function () {
			var PropertyFilterClass = this.properties.getPropertyFilter(this._getPropertyName());

			// Nested query builders could be exist only in QueryBuilderFilter or QueryCascadeFilter
			if (PropertyFilterClass === module.QueryBuilderFilter || PropertyFilterClass === module.QueryCascadeFilter) {
				module.queryPropertyFilter(this.structure.propertyFilter, PropertyFilterClass, 'indentNestedQueryBuilders');
			}
		}
	};

	module.queryClause = function (elements, option) {
		var args = Array.prototype.slice.call(arguments).splice(1);
		return appseeUtils.createClassInstance(elements, 'as-query-clause', QueryClause, option, args);
	};

	return qb;

}($.QueryBuilder || {}));
