import './internal-utils';
import appseeUtils from './appseeUtils';
import $ from 'jquery';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryBuilderFilter = function (element, options) {
		this._init(element, options);
	};

	QueryBuilderFilter.prototype = {

		_defaultOptions: {
			property: Object() // QueryProperty object
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);

			this.property = this.options.property;

			this.structure = {
				queryBuilder: null
			};
		},

		_render: function (queryBuilderFilterRendered) {

			var qbOptions = { properties: this.properties, defaultClauses: this.property.defaultClauses };
			if (this.options.emptyNotificationCallback) {
				qbOptions['emptyNotificationCallback'] = this.options.emptyNotificationCallback;
			}
			this.structure.queryBuilder = module.appseeQueryBuilder($('<div/>'), qbOptions);
			this.$element.empty().append(this.structure.queryBuilder);

			module.appseeQueryBuilder(this.structure.queryBuilder, 'initNested');
			if (queryBuilderFilterRendered) {
				queryBuilderFilterRendered();
			}
		},

		/******************
         * Public methods *
         ******************/

		getClassName: function () {
			return 'QueryBuilderFilter';
		},

		fetchPropertiesAndRender: function (fetchParams, queryBuilderFilterRendered) {
			if (this.property.fetchPropertiesUrl) {
				var self = this;
				var $loader = module.internalUtils.createLoader();

				this.$element.append($loader.fadeIn('slow'));

				var successFunc = function (properties) {
					$loader.remove();
					self.properties = properties;
					self._render(queryBuilderFilterRendered);
				};

				var errorFunc = function (properties) {
					$loader.remove();
					self.$element.empty();
					self.$element.append('<span class="as-query-fetch-error-msg">' + t('Error fetching actions') + '<span/>');
				};

				this.property.fetchProperties(fetchParams, successFunc, errorFunc);

			} else {
				this.properties = this.property.properties;
				this._render(queryBuilderFilterRendered);
			}
		},

		setState: function (state, setStateDoneCallback) {
			module.appseeQueryBuilder(this.structure.queryBuilder, 'stateSyncSuccessful', setStateDoneCallback);
			module.appseeQueryBuilder(this.structure.queryBuilder, 'setState', state);
		},

		validate: function () {
			if (this.structure.queryBuilder) {
				return module.appseeQueryBuilder(this.structure.queryBuilder, 'validate');
			}
			return true;
		},

		generateJson: function () {

			var innerQuery = {};
			if (this.structure.queryBuilder) {
				innerQuery = module.appseeQueryBuilder(this.structure.queryBuilder, 'generateJson');
			}

			// Check if the inner query is empty
			if (Object.keys(innerQuery).length === 0) {
				return {};
			}

			// Wrap the query with relation
			var query = module.internalUtils.wrapQueryWithRelation(innerQuery, this.property.relation);

			// Wrap the query with property name and return it
			return module.internalUtils.wrapQueryWithPropertyName(query, this.property.name);
		},

		countFilters: function () {

			if (this.structure.queryBuilder) {
				return module.appseeQueryBuilder(this.structure.queryBuilder, 'countFilters');
			} else {
				// If this is cascade step from contiuation method that is not exist yet
				return 1;
			}
		},

		indentNestedQueryBuilders: function () {
			if (this.structure.queryBuilder) {
				module.appseeQueryBuilder(this.structure.queryBuilder, 'indentNestedQueryBuilders');
			}
		}
	};

	module.QueryBuilderFilter = QueryBuilderFilter;

	module.queryPropertyFilter = function (elements, cls, option) {
		// Initiate the given property filter class (cls) with the given option
		var args = Array.prototype.slice.call(arguments).splice(2);
		return appseeUtils.createClassInstance(elements, 'as-query-property-filter', cls, option, args);
	};

	return qb;

}($.QueryBuilder || {}));
