import './internal-utils';
import appseeUtils from './appseeUtils';
import $ from 'jquery';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var Selector = function (element, options) {
		this._init(element, options);
	};

	Selector.prototype = {

		_defaultOptions: {
			description: null,            // Title of the selector
			noValuesText: '(No Values)',  // Text to show when no values was fetched to the select
			values: '',                   // Fetch values URL (string) or object of the form {value: description}
			defaultValue: null,           // If defaultValue is given, select this value after initialization
			multiple: false,              // If true, allow multiple selection of values
			changeCallback: $.noop,       // This function being called after a value change (argument: seleced value)
			groupedItems: false,          // If true, the select will be a group based
			autoSelectSingleValue: true,  // If true, when the component is loaded with a single value, it will be selected automatically

			// For AppseeSelect
			liveSearch: null,             // If null, get the value using the threshold
			liveSearchThreshold: 10,      // If number of items is greater or equals to this threshold, use liveSearch in AppseeSelect

			// For autocomplete
			autocompleteThreshold: 6000,  // If number of items is greater or equals to this threshold, use autocomplete and not AppseeSelect because it's too slow
			maxItems: 8                   // Max items to show in autocomplete
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			return $.extend({}, this._defaultOptions, this.$element.data(), options);
		},

		_init: function (element, options) {
			this.$element = $(element);
			this.options = this._getOptions(options);

			this.$element.empty().addClass('as-selector');

			// Init loader
			this.$loader = module.internalUtils.createLoader();
			this.$element.append(this.$loader);
			this._showLoader(false);

			// Will be created and appended to $element by create methods
			this.$selectorComponent = null;
		},

		_prepareItems: function (items) {
			if (typeof items != 'object' && !Array.isArray(items))
				return items;

			var itemValueAccessor = Array.isArray(items) ? function (x) { return items[x]; } : function (x) { return x; };
			var preparedItems = {};
			for (var item in items) {
				var itemValue = itemValueAccessor(item);
				var itemText = items[item];
				preparedItems[itemValue] = itemText;
			}
			return preparedItems;
		},

		_registerEvents: function () {
			var self = this;
			this.$selectorComponent.change(function () {
				self.options.changeCallback(self.getVal());
			});
		},

		_showLoader: function (show) {
			this.$loader.toggle(show);
		},

		_showComponent: function (show) {
			if (this.$selectorComponent == null)
				return;

			if (show) {
				this.$selectorComponent.show();
			} else {
				this.$selectorComponent.hide();
			}
		},

		_createAutocomplete: function (items) {
			this.$selectorComponent = $('<input/>').addClass('as-selector-component').attr('type', 'text').attr('as-selector-val', true);
			if (this.options.description)
				this.$selectorComponent.attr('placeholder', this.options.description);

			this.$element.empty().append(this.$selectorComponent);

			var maxItems = this.options.maxItems;

			// Initiate autocomplete
			var self = this;
			this.$selectorComponent.autocomplete({
				source: function (request, response) {
					var results = $.ui.autocomplete.filter(Object.keys(items).map(function (e) { return { value: e, label: items[e] }; }), request.term);
					response(results.slice(0, maxItems));
				},
				focus: function (event, ui) {
					$(this).val(ui.item.label);
					return false;
				},
				select: function (event, ui) {
					$(this).val(ui.item.label);
					$(this).attr('as-value', ui.item.value);
					self.options.changeCallback(ui.item.value);
					return false;
				}
			});
		},

		_createSelect: function (items) {
			this.$selectorComponent = $('<select/>').addClass('as-selector-component').attr('type', 'text').attr('as-selector-val', true);

			if (this.options.description)
				this.$selectorComponent.attr('title', this.options.description);

			if (this.options.multiple)
				this.$selectorComponent.prop('multiple', true);

			this.$element.append(this.$selectorComponent);

			// Initiate AppseeSelect
			this.$selectorComponent.appseeSelect({
				container: module.globals.floatsContainer,
				size: module.globals.dropdownSize,
				liveSearch: this.options.liveSearch != null ? this.options.liveSearch : Object.keys(items).length >= this.options.liveSearchThreshold,
				selectAllEnabled: this.options.multiple,
				selectGroupEnabled: this.options.groupedItems && this.options.multiple,
				selectedTextFormat: 'count > 2',
				classExtension: 'modified-bootstrap-select',
				replaceCaret: true
			});

			// Add the items to the select
			var itemCount = Array.isArray(items) ? items.length : Object.keys(items).length;
			if (itemCount === 0) {
				// In case of no items, we add a disabled option with no values text
				this.$selectorComponent.append('<option disabled value=\'-1\'>' + this.options.noValuesText + '</option>');
			} else {
				this.$selectorComponent.appseeSelect(this.options.groupedItems ? 'addGroupedItems' : 'addItems', items);
			}
			this.$selectorComponent.appseeSelect('refresh');

			// In case of grouped items - Unselect the selected value (we have a problem: when initiating a group based select,
			// the first item is selected automatically)
			if (this.options.groupedItems) {
				this.$selectorComponent.val('');
				this.$selectorComponent.appseeSelect('render');
			}
		},

		_loadValuesFromObject: function (itemsObject, valuesLoadedCallback) {
			// Sorted array with key and value,
			// We should not touch it,
			// Javascript converts it to original not sorted state
			const isSortedArray = Array.isArray(itemsObject) && itemsObject.length > 0 && itemsObject[0]['Key'] !== undefined;
			let items = isSortedArray ? itemsObject : this._prepareItems(itemsObject);

			this.clear();

			if (typeof items == 'object' || Array.isArray(items)) {
				if (Object.keys(items).length <= this.options.autocompleteThreshold || this.options.multiple) {
					this._createSelect(items);
				} else {
					this._createAutocomplete(items);
				}
			} else {
				this.$selectorComponent = $('<div/>').attr('as-value', items);
				this.$element.append(this.$selectorComponent);
			}

			// Set to default value if given
			if (this.options.defaultValue != null && this.options.defaultValue != undefined) {
				this.setVal(this.options.defaultValue);
			}

			// Handle auto select single value
			if (this.options.autoSelectSingleValue) {

				// Check if the select has only single value.
				// We have two cases: one for grouped items and one for regular items
				var itemValues = Object.values(items);
				var doesSelectHaveSingleValue = (!this.options.groupedItems && itemValues.length === 1) ||
                    (this.options.groupedItems && itemValues.length === 1 && itemValues[0].length === 1);

				// If the select has single value, we select this value
				if (doesSelectHaveSingleValue) {
					const findValueToSelect = (groupedItems) => {
						if (groupedItems) {
							return itemValues[0][0];
						}
						if (Array.isArray(items) && items[0].hasOwnProperty('Key')) {
							return items[0].Key;
						}
						return Object.keys(items)[0];
					};
					this.setVal(findValueToSelect(this.options.groupedItems));
					this.setDisabled(true);
				}
			}

			// Register events
			this._registerEvents();

			if (valuesLoadedCallback) {
				valuesLoadedCallback(items);
			}
		},

		_loadValuesFromUrl: function (fetchUrl, valuesLoadedCallback, valuesTransformer) {
			if (!fetchUrl)
				return;

			// Prepare selected values for valuesTransformer
			var paramsDict = {};
			if ($.isFunction(valuesTransformer)) {
				var urlParts = fetchUrl.split('?');
				if (urlParts.length > 1 && urlParts[1] !== '') {
					// Add all the params to the params dictionary
					var paramsArr = urlParts[1].split('&');
					for (var i = 0; i < paramsArr.length; i++) {
						var paramParts = paramsArr[i].split('=');
						paramsDict[paramParts[0]] = paramParts[1];
					}
				}
			}

			this._showComponent(false);
			this._showLoader(true);

			var self = this;
			var successFunc = function (data) {
				if (data != null) {
					data = $.isFunction(valuesTransformer) ? valuesTransformer(data, paramsDict) : data;
					self._loadValuesFromObject(data, valuesLoadedCallback);
					self._showLoader(false);
					self._showComponent(true);
				}
			};

			var errorFunc = function (data) {
				self.clear();
				self._showLoader(false);
				self.$element.append('<span class="as-query-fetch-error-msg">' + t('Error fetching values') + '<span/>');
			};

			// Load values
			module.internalUtils.performAjaxCall(fetchUrl, successFunc, errorFunc);
		},

		_isSelect: function () {
			return this.$selectorComponent.is('select');
		},

		/******************
         * Public methods *
         ******************/

		registerChangeEvent: function (callback) {
			this.changeCallback = callback;
		},

		loadValues: function (values, valuesLoadedCallback, valuesTransformer) {

			var valuesSource = values || this.options.values;

			if (!valuesSource)
				return;

			if (typeof valuesSource === 'string' || valuesSource instanceof String) {
				this._loadValuesFromUrl(valuesSource, valuesLoadedCallback, valuesTransformer);
			} else {
				this._loadValuesFromObject(valuesSource, valuesLoadedCallback);
			}
		},

		setDisabled: function (disabled) {
			if (this.$selectorComponent == null)
				return;

			if (this._isSelect()) {
				this.$selectorComponent.prop('disabled', disabled);
				this.$selectorComponent.appseeSelect('refresh');
			} else {
				this.$selectorComponent.prop('disabled', disabled);
			}
		},

		getVal: function () {
			var val;

			if (this.$selectorComponent) {
				// Get the value according to the component type (select or autocomplete)
				if (this._isSelect()) {
					val = this.$selectorComponent.val();
				} else {
					val = this.$selectorComponent.attr('as-value');
				}
			}

			if (val == null) {
				val = '';
			}

			return val;
		},

		setVal: function (value) {
			if (this._isSelect()) {
				this.$selectorComponent.val(value);
				this.$selectorComponent.appseeSelect('refresh');
			} else {
				this.$selectorComponent.attr('as-value', value);
			}
		},

		fetch: function (fetchUrl, valuesFetchedCallback) {
			this._loadValuesFromUrl(fetchUrl, valuesFetchedCallback);
		},

		clear: function () {
			if (this.$selectorComponent) {
				if (this._isSelect()) {
					this.$selectorComponent.appseeSelect('destroy');
				} else {
					this.$selectorComponent.remove();
				}
				this.$selectorComponent = null;
			}

			this._showLoader(false);
			this.$element.children('.as-query-fetch-error-msg').remove();
		}
	};

	module.selector = function (elements, option) {
		var args = Array.prototype.slice.call(arguments).splice(1);
		return appseeUtils.createClassInstance(elements, 'as-selector', Selector, option, args);
	};

	return qb;

}($.QueryBuilder || {}));
