import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {actionTypes} from '@servicenow/ui-core';
import {GET_DATE_RANGE_PICKER_SYSTEM_PROPS} from './queries';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

const getSystemProperties = createGraphQLEffect(GET_DATE_RANGE_PICKER_SYSTEM_PROPS, {
	successActionType: 'SET_DATE_RANGE_PICKER_SYSTEM_PROPS'
});

export default {
	['NOW_DATE_RANGE_PICKER#VALUE_CHANGED']: ({action, dispatch}) => {
		dispatch('SN_APPSEE_DATE_RANGE_PICKER#VALUE_CHANGED', action.payload);
	},
	[COMPONENT_BOOTSTRAPPED]: getSystemProperties,
	['SET_DATE_RANGE_PICKER_SYSTEM_PROPS']: ({action, updateState}) => {
		const {
			language,
			dateFormat
		} = action.payload.data.GlideDomain_Query.user;
		updateState({
			language: language,
			dateFormat: dateFormat.toUpperCase()
		});
	}
};
