import './internal-utils';
import $ from 'jquery';
import cascadeCustomContinuationMethods from './cascade-custom-continuation-methods';
import './types-dictionary';
import './query-builder-filter';
import './query-cascade-filter';
import './query-term-filter';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	var QueryProperty = function (propertyObj) {

		this.name = propertyObj.name;
		this.description = propertyObj.description || this.name;
		this.singularDescription = propertyObj.singularDescription || this.description;
		this.pluralDescription = propertyObj.pluralDescription || this.description;
		this.isCustom = propertyObj.isCustom === true;
		this.manuallySortIndex = propertyObj.manuallySortIndex;

		// Type & Conditions
		this.type = propertyObj.type;
		this.typeParams = propertyObj.typeParams;
		this.isTypeInferred = propertyObj.isTypeInferred;
		this.conditionsCustomizations = propertyObj.conditionsCustomizations;
		this.defaultValues = propertyObj.defaultValues;

		// Set the type description
		if (this.type && this.type in module.typesDictionary) {
			this.typeDescription = module.typesDictionary[this.type].description;
		} else {
			this.typeDescription = propertyObj.typeDescription || '';
		}

		// For context related properties
		this.contextBindedPropName = propertyObj.contextBindedPropName;
		this.showOnlyInContext = propertyObj.showOnlyInContext;

		// Filter Types
		this.relation = propertyObj.relation;
		this.cascade = propertyObj.cascade;

		// For cascade filter
		this.includeAllSteps = propertyObj.includeAllSteps;
		this.noCascadeHelpText = propertyObj.noCascadeHelpText;
		this.nestedPropName = propertyObj.nestedPropName;

		// For cascade steps
		this.continueBtnLabel = propertyObj.continueBtnLabel;
		this.fetchExtraDataUrl = propertyObj.fetchExtraDataUrl;
		this.continuationCheckMethod = cascadeCustomContinuationMethods[propertyObj.continuationCheckMethodName];
		this.cascadeStepDefaultValue = propertyObj.cascadeStepDefaultValue;
		this.cascadeOptionalStep = propertyObj.cascadeOptionalStep;
		this.helpText = propertyObj.helpText;
		this.shouldShowStep = propertyObj.shouldShowStep || function(selectedValue) { return selectedValue != null; };

		// Values (for term filter)
		this.values = propertyObj.values;
		this.fetchValuesUrl = propertyObj.fetchValuesUrl;
		this.valuesTransformer = propertyObj.valuesTransformer;

		// Properties (for object filter)
		this.properties = propertyObj.properties;
		this.fetchPropertiesUrl = propertyObj.fetchPropertiesUrl;
		this.defaultClauses = propertyObj.defaultClauses;
	};

	QueryProperty.prototype = {

		/******************
         * Public methods *
         ******************/

		fetchProperties: function (fetchParams, callback, callbackOnError) {
			var paramsStr = '';
			if (fetchParams) {
				var objectKeys = Object.keys(fetchParams);
				if (objectKeys.length) {
					var seperator = this.fetchPropertiesUrl.includes('?') ? '&' : '?';
					paramsStr = seperator + objectKeys.map(function (key) { return key + '=' + fetchParams[key]; }).join('&');
				}
			}

			var self = this;
			var successFunc = function (data) {
				if (data != null) {
					self.properties = [];
					data.forEach(property => {
						self.properties.push({name: property, description: property, type: 'string'});
					});
					callback(self.properties);
				}
			};

			var errorFunc = function (data) {
				callbackOnError([]);
			};

			module.internalUtils.performAjaxCall(this.fetchPropertiesUrl + paramsStr, successFunc, errorFunc);
		},

		getPropertyFilter: function () {
			if (this.properties || this.fetchPropertiesUrl) {
				return module.QueryBuilderFilter;
			} else if (this.cascade) {
				return module.QueryCascadeFilter;
			} else {
				return module.QueryTermFilter;
			}
		}
	};

	module.queryProperty = function(propertyObj) {
		return new QueryProperty(propertyObj);
	};

	var QueryProperties = function (propertiesList, isCustomPropertiesSupported) {
		if (propertiesList && Array.isArray(propertiesList)) {
			this.properties = propertiesList.map(function (propertyObj) { return module.queryProperty(propertyObj); });
		} else {
			this.properties = [];
		}
		this.isCustomPropertiesSupported = isCustomPropertiesSupported === true;
	};

	QueryProperties.prototype = {

		/*******************
         * Private methods *
         *******************/

		_shouldAddPropertyToSelect: function (property) {

			// Check if the property should be added according to its showOnlyInContext conditions (if defined)
			return module.internalUtils.filterShowOnlyInContext(property);
		},

		_appendOptionsToSelect: function (properties, selectElement) {

			function compare(a, b) {
				if (a < b) return -1;
				if (a > b) return 1;
				return 0;
			};

			var propertiesSorted = properties.slice().sort(function (a, b) {
				// Manually-sorted properties compare
				if (a.manuallySortIndex !== undefined && b.manuallySortIndex !== undefined) {
					return compare(a.manuallySortIndex, b.manuallySortIndex);
				} else if (a.manuallySortIndex === undefined && b.manuallySortIndex === undefined) {
					// Default description sort
					return compare(a.description.toLowerCase(), b.description.toLowerCase());
				} else {
					// Manually sorted properties are first
					return a.manuallySortIndex !== undefined ? -1 : 1;
				}
			});

			// The following indicators will help to decide when and if to show a divider
			// between manually sorted properties to automatic sorted properties
			var dividerWasAdded = false;
			var manuallySortedPropertiesExist = false;

			// Append all the properties to the property select
			var self = this;
			propertiesSorted.forEach(function (property) {

				// Check if this property should be added to the properties select
				if (self._shouldAddPropertyToSelect(property)) {

					if (property.manuallySortIndex !== undefined) {
						manuallySortedPropertiesExist = true;
					} else if (!dividerWasAdded && manuallySortedPropertiesExist) {
						selectElement.append('<option data-divider=\'true\'></option>');
						dividerWasAdded = true;
					}

					// Build the option element
					var propertyName = self.getPropertyName(property);
					var $option = $('<option value="' + propertyName + '" data-subtext="' +
                        property.typeDescription + '">' + property.description + '</option>');

					// Add the option to the select
					selectElement.append($option);
				}
			});
		},

		/******************
         * Public methods *
         ******************/

		getPropertyName: function (property) {
			return property.name;
		},

		getProperty: function (propertyName) {
			var self = this;
			return this.properties.find(function (p) { return self.getPropertyName(p) === propertyName || p.nestedPropName === propertyName; });
		},

		getPropertyFilter: function (propertyName) {
			if (!propertyName) {
				return null;
			}
			var property = this.getProperty(propertyName);
			return property.getPropertyFilter();
		},

		generatePropertySelect: function () {
			var $propertySelect = $('<select class="as-query-property-select" name="property" ' +
                                    'title="' + t('Select Property') + '" data-live-search="true" showSubtext="true"></select>');

			// Add the pre-defined properties
			var preDefinedProps = this.properties.filter(function (p) { return !p.isCustom; });
			this._appendOptionsToSelect(preDefinedProps, $propertySelect);

			// In case of custom properties supported
			if (this.isCustomPropertiesSupported) {

				// Add seperator
				$propertySelect.append('<option data-divider=\'true\'></option>');

				// Add title for the custom properties section
				var content = '<span class="custom-prop-title">' + t('Custom Properties') + '</span>';
				$propertySelect.append('<option disabled style=\'cursor: default;\' data-content=\'' + content + '\'></option>');

				// Add the custom user properties (if any)
				var customUserProps = this.properties.filter(function (p) { return p.isCustom; });
				if (customUserProps.length > 0) {

					// Add all the custom user properties
					this._appendOptionsToSelect(customUserProps, $propertySelect);
				} else {

					// In case of no items, we add a disabled option with no values text
					$propertySelect.append('<option disabled value=\'-1\'>( ' + t('No Properties') + ')</option>');
				}
			}

			return $propertySelect;
		}
	};

	module.queryProperties = function (propertiesList, isCustomPropertiesSupported) {
		return new QueryProperties(propertiesList, isCustomPropertiesSupported);
	};

	return qb;

}($.QueryBuilder || {}));
