import './types-dictionary';
import validators from './validators';
import $ from 'jquery';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	var module = qb.module;

	module.components = {

		// Getters

		getPropertyTypeItems: function () {

			var propertyTypes = {};

			// Iterate all the property types
			for (var propertyType in module.typesDictionary) {

				// Add only custom user property supported types
				if (module.typesDictionary.hasOwnProperty(propertyType) &&
                    module.typesDictionary[propertyType].isTypeInferredSupported) {
					propertyTypes[propertyType] = module.typesDictionary[propertyType].description;
				}
			}

			return propertyTypes;
		},

		getPropertyTypeText: function (propertyType) {
			return module.typesDictionary[propertyType].description;
		},

		getConditionObjects: function (property, propertyType) {

			// Retrieve the relevant conditions from the types dictionary (In order not to modify the global
			// types dictionary, we make a deep copy of the current conditions array)
			var conditions = module.typesDictionary[propertyType].conditions.map(function (a) {
				return Object.assign({}, a);
			});

			// Handle conditions customizations
			if (property.conditionsCustomizations) {

				// Exclude the non-relevant conditions (if showOnlyConditions is defined)
				if (property.conditionsCustomizations.showOnlyConditions) {
					conditions = conditions.filter(function (condObj) {
						return $.inArray(condObj.name, property.conditionsCustomizations.showOnlyConditions) !== -1;
					});
				}

				// Change the condition names according to the names mapping (if defined)
				if (property.conditionsCustomizations.namesMapping) {
					for (var j = 0; j < conditions.length; j++) {
						if (conditions[j].name in property.conditionsCustomizations.namesMapping) {
							conditions[j].name = property.conditionsCustomizations.namesMapping[conditions[j].name];
						}
					}
				}

				// // Add custom conditions (if any) on the start of the list
				// if (property.conditionsCustomizations.customConditions) {
				// 	if (property.conditionsCustomizations.showCustomConditionsFirst) {
				// 		conditions = property.conditionsCustomizations.customConditions.concat(conditions);
				// 	} else {
				// 		conditions = conditions.concat(property.conditionsCustomizations.customConditions);
				// 	}
				// }
				// Add custom conditions (if any) on the start of the list
				if (property.conditionsCustomizations.customConditions) {
					const customConditions = property.conditionsCustomizations.customConditions.filter(condition =>
						module.internalUtils.filterShowOnlyInContext(condition));

					if (property.conditionsCustomizations.showCustomConditionsFirst) {
						conditions = customConditions.concat(conditions);
					} else {
						conditions = conditions.concat(customConditions);
					}
				}
			}

			// Return the condition objects
			return conditions;
		},

		getConditionItems: function (property, propertyType) {

			// Return all the relevant condition item names for the given property
			return this.getConditionObjects(property, propertyType).map(function (condObj) { return condObj.name; });
		},

		getConditionObject: function (property, propertyType, conditionName) {
			return this.getConditionObjects(property, propertyType).find(function (condObj) {
				return condObj.name === conditionName;
			});
		},

		getValueComponent: function (property, propertyType, condition) {
			return this.getConditionObject(property, propertyType, condition).valueComponent;
		},

		getValueComponentSetter: function (property, propertyType, condition) {
			var valueComponent = this.getValueComponent(property, propertyType, condition);
			if (valueComponent) {
				return valueComponent.valueSetter;
			} else {
				return null;
			}
		},

		getDefaultValue: function (property, propertyType, condition) {

			// Return the default value of the condition from the property definition (if exists)
			if (property.defaultValues && condition in property.defaultValues) {
				return property.defaultValues[condition];
			}

			// Return the default value of the value component (if exists)
			// Note: some of the value components doesn't have default value so we check if it is not undefined/null
			var valueComponent = this.getValueComponent(property, propertyType, condition);
			if (valueComponent && valueComponent.defaultValue !== undefined && valueComponent.defaultValue != null) {
				return valueComponent.defaultValue;
			}

			// There is no default value
			return null;
		},

		validateValueComponent: function (value, validatorName, valueComponent) {
			var validator = validators[validatorName];
			if (!validator)
				return true;

			var valid = validator.accessor(value);
			$(valueComponent).siblings('.as-query-validation-msg').remove();
			if (!valid) {
				$(valueComponent).after($('<div/>').addClass('as-query-validation-msg').text(validator.msg));
			}
			return valid;
		}
	};

	return qb;

}($.QueryBuilder || {}));
