import $ from 'jquery';
import valueHelper from './value-helper';
import appsee from './appsee';
import { t } from 'sn-translate';

$.QueryBuilder = (function (qb) {

	qb.module = qb.module || {};
	const module = qb.module;

	function QueryBuilderParser (options) {
		this._init(options);
	}

	QueryBuilderParser.prototype = {

		_defaultOptions: {},

		_conditionsMapping: {
			string: [
				{ conditionName: t('equals to'), conditionDSLItems: ['eq'] },
				{ conditionName: t('not equals to'), conditionDSLItems: ['neq'] },
				{ conditionName: t('contains'), conditionDSLItems: ['contains'] }
			],
			number: [
				{ conditionName: '=', conditionDSLItems: ['eq'] },
				{ conditionName: '>', conditionDSLItems: ['gt'] },
				{ conditionName: '>=', conditionDSLItems: ['gte'] },
				{ conditionName: '<', conditionDSLItems: ['lt'] },
				{ conditionName: '<=', conditionDSLItems: ['lte'] },
				{
					conditionName: t('is between'), conditionDSLItems: ['gte', 'lte'],
					parseQueryValue: function (clauseData) {
						return { start: clauseData['gte'], end: clauseData['lte'] };
					}
				}
			],
			boolean: [
				{ conditionName: t('is'), conditionDSLItems: ['eq'] }
			],
			existenceBasedBoolean: [],
			date: [
				{ conditionName: t('is in the last'), conditionDSLItems: ['in_last'] },
				{
					conditionName: t('is later than'), conditionDSLItems: ['gt'],
					parseQueryValue: function (clauseData) {
						return valueHelper.getDatePickerFormattedDate(clauseData['gt']);
					}
				},
				{
					conditionName: t('is earlier than'), conditionDSLItems: ['lt'],
					parseQueryValue: function (clauseData) {
						return valueHelper.getDatePickerFormattedDate(clauseData['lt']);
					}
				},
				{
					conditionName: t('is in range'), conditionDSLItems: ['gte', 'lte'],
					parseQueryValue: function (clauseData) {
						return {
							startDate: valueHelper.getDatePickerFormattedDate(clauseData['gte']),
							endDate: valueHelper.getDatePickerFormattedDate(clauseData['lte'])
						};
					}
				}
			],
			list: [
				{ conditionName: t('contain any of'), conditionDSLItems: ['contains_any'] },
				{ conditionName: t('contain all of'), conditionDSLItems: ['contains_all'] }
			],
			unboundList: [
				{ conditionName: t('contain any of'), conditionDSLItems: ['contains_any'] },
				{ conditionName: t('contain all of'), conditionDSLItems: ['contains_all'] }
			],
			bound: [
				{ conditionName: t('is any of'), conditionDSLItems: ['is_any'] },
				{ conditionName: t('is none of'), conditionDSLItems: ['is_not_any'] }
			],
			duration: [
				{
					conditionName: '>', conditionDSLItems: ['gt'],
					parseQueryValue: function (clauseData) {
						return parseInt(clauseData['gt']) / 1000;
					}
				},
				{
					conditionName: '>=', conditionDSLItems: ['gte'],
					parseQueryValue: function (clauseData) {
						return parseInt(clauseData['gte']) / 1000;
					}
				},
				{
					conditionName: '<', conditionDSLItems: ['lt'],
					parseQueryValue: function (clauseData) {
						return parseInt(clauseData['lt']) / 1000;
					}
				},
				{
					conditionName: '<=', conditionDSLItems: ['lte'],
					parseQueryValue: function (clauseData) {
						return parseInt(clauseData['lte']) / 1000;
					}
				},
				{
					conditionName: t('is between'), conditionDSLItems: ['gte', 'lte'],
					parseQueryValue: function (clauseData) {
						return { start: parseInt(clauseData['gte']) / 1000, end: parseInt(clauseData['lte']) / 1000 };
					}
				}
			],
			actionKeys: [
				{ conditionName: t('is'), conditionDSLItems: ['eq'] }
			],
			thirdParty: [
				{
					conditionName: t('in'), conditionDSLItems: ['eq'],
					parseQueryValue: function (clauseData) {
						var thirdPartyParams = clauseData['eq'].split(':');
						return { thirdPartySystem: thirdPartyParams[0], id: thirdPartyParams[1] };
					}
				}
			],
			platform: [
				{ conditionName: t('is'), conditionDSLItems: ['eq'] }
			]
		},

		/*******************
         * Private methods *
         *******************/

		_getOptions: function (options) {
			options = $.extend({}, this._defaultOptions, options);
			return options;
		},

		_init: function (options) {
			this.options = this._getOptions(options);

			this.clauseDataToPropertyFilterStateMapping = {
				'QueryBuilderFilter': this._queryBuilderFilterToState.bind(this),
				'QueryCascadeFilter': this._queryCascadeFilterToState.bind(this),
				'QueryTermFilter': this._queryTermFilterToState.bind(this)
			};
		},

		_jsonQueryToState: function (jsonQuery, qbPropertiesContainer) {

			// Validate the conjunction of the query (must be only one, and one of the following: and/or)
			var keys = Object.keys(jsonQuery);
			if (keys.length > 1 || $.inArray(keys[0], ['and', 'or']) === -1) {

				// Invalid conjunction, return null
				return null;

			} else {
				var conjunction = keys[0];
				var queryState = { conjunction: conjunction, clauses: [] };

				// For each query clause
				var clauses = jsonQuery[conjunction];
				for (var i = 0; i < clauses.length; i++) {

					// Convert the json clause to state
					var clauseState = this._jsonClauseToState(clauses[i], qbPropertiesContainer);

					// In case of invalid clause, we "reject" the entire query (and therefore returning null)
					if (!clauseState) {
						return null;
					}

					queryState['clauses'].push(clauseState);
				}

				return queryState;
			}
		},

		_handleCustomPropertiesClause: function (propertyName, jsonClause, qbPropertiesContainer) {

			// Handle regular custom property term filter clause
			if (propertyName === 'UserCustomProperties') {
				return this._jsonClauseToState(jsonClause['UserCustomProperties']['nested_match']['and'][0],
					qbPropertiesContainer);

			} else if (propertyName === 'UserCustomPropertyNames') { // Handle "is set"/"is not set" conditions
				var condition = Object.keys(jsonClause['UserCustomPropertyNames'])[0];
				var newPropertyName = Object.values(jsonClause['UserCustomPropertyNames'])[0][0];
				return {
					property: newPropertyName,
					propertyFilterState: { condition: condition === 'contains_any' ? 'is set' : 'is not set' }
				};
			}

			// Invalid custom properties clause
			return null;
		},

		_jsonClauseToState: function (jsonClause, qbPropertiesContainer) {
			// Get the full property name.
			// *Usually* this is the original property name, except for a nested platform clause where it should
			// be in the following format: PlatformProperties.<Platform>.<PropertyName>
			// (e.g., PlatformProperties.iOS.TotalSessions)
			var fullPropertyName = Object.keys(jsonClause)[0];

			// In case of custom properties, we handle it in another method
			if (fullPropertyName === 'UserCustomProperties' || fullPropertyName === 'UserCustomPropertyNames') {
				return this._handleCustomPropertiesClause(fullPropertyName, jsonClause, qbPropertiesContainer);
			}

			// Fetch the original property name from the full name
			var propertyNameParts = fullPropertyName.split('.');
			var propertyName = (propertyNameParts.length === 3 && propertyNameParts[0] === 'UserPlatformProperties')
				? propertyNameParts[2] : propertyNameParts[0];

			// Validate the property name exist in the QB properties
			var property = qbPropertiesContainer.getProperty(propertyName);
			if (!property) {

				// Invalid property name
				return null;

			} else {

				// Convert the clause data to state, according to the property filter type
				var propertyFilterState =
                    this.clauseDataToPropertyFilterStateMapping[property.getPropertyFilter().prototype.
                    	getClassName()](fullPropertyName, jsonClause[fullPropertyName], property);

				// Return the clause state (if propertyFilterState is valid)
				return propertyFilterState ?
					{ property: propertyName, propertyFilterState: propertyFilterState } : null;
			}
		},

		_queryTermFilterToState: function (fullPropertyName, jsonClauseData, property) {
			var propertyType = property.type;

			// If this clause contain extra info with type inside, we take the type from it (it should happen
			// in case of type inferred properties)
			if ('extraInfo' in jsonClauseData) {
				if ('propertyType' in jsonClauseData['extraInfo']) {
					propertyType = jsonClauseData['extraInfo']['propertyType'];
				}
				delete jsonClauseData['extraInfo'];
			}

			// Get lists of the conditions and values of the clause
			var conditionDSLItems = Object.keys(jsonClauseData);
			var valueDSLItems = Object.values(jsonClauseData);

			// Handling specific cases section
			if (property.name === 'AppUserId' && conditionDSLItems[0].indexOf('exist') !== -1) {
				return { condition: t('is anonymous'), value: conditionDSLItems[0].startsWith('not') };
			} else if (property.name === 'UserPlatforms') {
				if (conditionDSLItems[0] === 'contains_all') {
					if (valueDSLItems[0].length === 1) {
						return { condition: t('contain'), value: valueDSLItems[0].toString() };
					} else if (valueDSLItems[0].length > 1) {
						return { condition: t('contain'), value: '[' + valueDSLItems[0].sort().join(', ') + ']' };
					}
				} else if (conditionDSLItems[0] === 'contain_only') {
					return { condition: t('contain only'), value: valueDSLItems[0].toString() };
				}
			} else if (property.name === 'UserDeviceCodes' && conditionDSLItems[0] === 'contain_only') {
				return { condition: t('contain only'), value: valueDSLItems[0] };
			} else if (propertyType === 'existenceBasedBoolean') {
				return { condition: t('is'), value: !conditionDSLItems[0].startsWith('not') };
			}

			// Validate the property type exist in the conditions mapping
			if (propertyType in this._conditionsMapping) {

				// Search for the condition item in the conditions mapping
				var conditionItemArr = this._conditionsMapping[propertyType].filter(function (item) {
					return module.internalUtils.arraysEqual(item.conditionDSLItems, conditionDSLItems);
				});

				// If the condition item was found
				if (conditionItemArr && conditionItemArr.length > 0) {
					var conditionItem = conditionItemArr[0];

					// Get the condition name and parse the value (if needed)
					var condition = conditionItem.conditionName;
					var value = conditionItem.parseQueryValue ? conditionItem.parseQueryValue(jsonClauseData) :
						Object.values(jsonClauseData)[0];

					var resultState = { condition: condition, value: value };

					// In case of type inferred property, we set also the property type (in order to customise
					// it while setting this state in the query builder)
					if (property.isTypeInferred) {
						resultState['propertyType'] = propertyType;
					}

					return resultState;
				}
			}

			// Invalid clause data
			return null;
		},

		_queryCascadeFilterToState: function (fullPropertyName, jsonClauseData, property) {

			// Handle screens filter
			if (property.name === 'SessionScreens') {

				// Get the screens filter context
				const screenIdExist = jsonClauseData && jsonClauseData['nested_match'] && jsonClauseData['nested_match']['and'] && jsonClauseData['nested_match']['and'][0] && jsonClauseData['nested_match']['and'][0]['ScreenId'];
				if (!screenIdExist)
					return [];

				jsonClauseData = jsonClauseData['nested_match']['and'][0]['ScreenId'];
				// we removed extradata from SessionScreens
				// var screensContext = jsonClauseData['extraInfo'];
				// delete jsonClauseData['extraInfo'];

				// Build the cascade filter state (array of states, one for each cascade step)
				return [
					this._queryTermFilterToState(property.cascade[0].name, { 'eq': appsee.context.platformValue }, property.cascade[0]),
					{ value: null },
					this._queryTermFilterToState(property.cascade[2].name, jsonClauseData, property.cascade[2])
				];
			} else if (property.name === 'SessionActionKeys') { // Handle actions filter

				// Get the actions filter context
				var actionContext = jsonClauseData['extraInfo'];
				delete jsonClauseData['extraInfo'];

				// Build the cascade filter state (array of states, one for each cascade step)
				return [
					this._queryTermFilterToState(property.cascade[0].name, { 'eq': actionContext['platform'] }, property.cascade[0]),
					{ value: actionContext['device'] },
					{ value: actionContext['screenName'] },
					{ value: actionContext['actionType'] },
					this._queryTermFilterToState(property.cascade[4].name, jsonClauseData, property.cascade[4])
				];
			} else if (property.name === 'SessionCustomEvents') { // Handle custom events
				var eventPropsQuery = jsonClauseData['nested_match']['and'];
				var stepsState = [];

				// Set the event name state
				var itemWithName = eventPropsQuery.find(eventProp => eventProp.Name);

				if (!itemWithName) {
					itemWithName = { Name: {eq: 'DynamicProperties'}};
				}

				stepsState.push(this._queryTermFilterToState(property.cascade[0].name, itemWithName['Name'], property.cascade[0]));

				// Set the event properties state (if exists)
				var itemWithProps = eventPropsQuery.find(eventProp => eventProp.Properties);
				if (eventPropsQuery.length > 1 && itemWithProps) {
					var filterRightSide = itemWithName['Name'].contains_any ? itemWithName['Name'].contains_any : itemWithName['Name'].eq;

					var contextBindedPropName = property.cascade[0].contextBindedPropName || 'name';

					stepsState.push(this._queryBuilderFilterToState(property.cascade[1].name, itemWithProps['Properties'], property.cascade[1],
						contextBindedPropName + '=' + filterRightSide[0]));
				}

				return stepsState;
			} else if (property.name === 'SessionAppVersion' || property.name === 'SessionOSVersion') { // Handle AppVersion/OSVersion (inside Session Exists)
				// Get the platform value from the extra info
				const platformValue = jsonClauseData['extraInfo']['platform'];
				delete jsonClauseData['extraInfo'];

				return [
					this._queryTermFilterToState(property.cascade[0].name, { 'eq': platformValue }, property.cascade[0]),
					this._queryTermFilterToState(property.cascade[1].name, jsonClauseData, property.cascade[1])
				];
			}

			// Handle nested platform relation
			if (property.relation === 'nestedPlatform') {

				// Get the platform value (first step of the cascade)
				var propertyNameParts = fullPropertyName.split('.');
				const platformValue = propertyNameParts.length === 3
					? module.internalUtils.getPlatformEnumValueByName(propertyNameParts[1])
					: '[0, 1]';

				return [
					this._queryTermFilterToState(property.cascade[0].name, { 'eq': platformValue }, property.cascade[0]),
					this._queryTermFilterToState(property.cascade[1].name, jsonClauseData, property.cascade[1])
				];
			}

			return null;
		},

		_queryBuilderFilterToState: function (fullPropertyName, jsonClauseData, property, fetchPropertiesParamsStr) {

			// Fetch the query builder properties
			var properties = [];
			if (property.properties) {

				// Fetch the properties from the property object
				properties = property.properties;
			} else if (property.fetchPropertiesUrl) {

				// Fetch the properties from url using ajax call
				var seperator = property.fetchPropertiesUrl.includes('?') ? '&' : '?';
				var url = property.fetchPropertiesUrl + seperator + (fetchPropertiesParamsStr ? fetchPropertiesParamsStr : '');

				var successFunc = function (data) {
					if (data != null) {
						properties = [];
						data.forEach(property => {
							properties.push({name: property, description: property, type: 'string'});
						});
					}
				};

				// Fetch extra data
				module.internalUtils.performAjaxCall(url, successFunc);
			}

			// Get the query from the clause data and convert it to state
			var relation = Object.keys(jsonClauseData)[0];
			return this._jsonQueryToState(jsonClauseData[relation], module.queryProperties(properties));
		},

		/*******************
         * Public methods  *
         *******************/

		jsonQueryStringToState: function (jsonQueryString, qbPropertiesContainer) {

			// Try to parse the query string to json object
			var jsonQuery = module.internalUtils.tryParseJSON(jsonQueryString);

			// If the parsing succeeded, convert the json query to QB state, otherwise, return null
			return jsonQuery ? this._jsonQueryToState(jsonQuery, qbPropertiesContainer) : null;
		}

	};

	module.QueryBuilderParser = QueryBuilderParser;

	return qb;

}($.QueryBuilder || {}));
