import $ from 'jquery';
import moment from 'moment';

export default {
	/*************************************/
	/*****  Context Init & Update  *******/
	/*************************************/
	context: { platform: 'chat', platformValue: 3 },
	domainBaseDir: '/analytics-api',

	isReplayEnabled: true,

	loadPart: function (url, target, additionalData, errorTarget) {
		var params = this.context;

		if (additionalData)
			params = $.extend({}, params, additionalData);

		$.ajax({
			type: 'POST',
			url: url,
			data: params,
			success: function (data) {
				if (target) {
					if ($.isFunction(target)) {
						target(data);
					} else {
						$(target).html(data);
					}
				}
			},
			error: function (error) {
				if ($.isFunction(errorTarget)) {
					errorTarget(error);
				}
			}
		});
	},

	calculateRelativeLabelDates: function(label) {
		var start;
		var end = moment().utc();

		// When granularity is time based we need to update the start and end time according to 'now' time
		// instead the times that were provided at the component loading
		switch (label) {
			case 'Last 15 Minutes':
				start = moment().utc().subtract(15, 'minutes');
				break;
			case 'Last 1 Hour':
				start = moment().utc().subtract(1, 'hours');
				break;
			case 'Last 4 Hours':
				start = moment().utc().subtract(4, 'hours');
				break;
			case 'Last 24 Hours':
				start = moment().utc().subtract(24, 'hours');
				break;
			case 'Last 7 Days':
				start = moment().utc().subtract(7, 'days').startOf('day');
				break;
			case 'Last 30 Days':
				start = moment().utc().subtract(30, 'days').startOf('day');
				break;
			case 'Last 90 Days':
				start = moment().utc().subtract(90, 'days').startOf('day');
				break;
			case 'Today':
				start = moment().utc().startOf('day');
				break;
			case 'Yesterday':
				start = moment().utc().subtract(1, 'days').startOf('day');
				end = moment().utc().subtract(1, 'days').endOf('day');
				break;
			case 'This Month':
				start = moment().utc().startOf('month');
				break;
			default:
			// Fallback for This Month
				console.error('Date label was not found');
				return this.calculateRelativeLabelDates('This Month');
		}

		return [start, end];
	},
};

window.onerror = function (errorMsg, url, lineNumber, columnNumber, errorObj) {

	var reportData = {
		'source': 'Javascript',
		'url': url,
		'errorDescription': errorMsg,
		'extraInfo': {
			'Line Number': lineNumber
		}
	};

	if (columnNumber !== undefined)
		reportData.extraInfo['Column Number'] = columnNumber;

	if (errorObj !== undefined && errorObj) {
		if (errorObj.hasOwnProperty('fileName'))
			reportData.extraInfo['File Name'] = errorObj.fileName;
		if (errorObj.hasOwnProperty('lineNumber'))
			reportData.extraInfo['Line Number'] = errorObj.lineNumber;  // Override, assuming more accurate
		if (errorObj.hasOwnProperty('stack'))
			reportData.extraInfo['Stack Trace'] = errorObj.stack;
	}
	console.error('Appsee Error: ', [reportData]);
};

